<template>
  <div class="choosePerson">
      <!-- 人员开始-->
        <div class="choose-person-container" >
          <div class="content-body">
            <el-row>
              <!-- 左侧树 begin -->
              <el-col :span="10" class="treeWidth">
                  <div class="tree-container">
                      <organization-tree
                        ref="tree"
                        v-model="code"
                        @node-click="handleClick">
                      </organization-tree>
                  </div>
              </el-col>
              <!-- 左侧树 end -->
              <!-- 右侧表格 begin -->
              <el-col :span="14">
                <org-table ref="orgTable"
                           :tags="tags"
                           :mutilselectCheck="mutilselectCheck"
                           :orgTreeID="orgTreeID"
                           :deleteTags="deleteTags"
                           class="choosePersonTable"
                           @chooseOrgTag="chooseOrgTag">
                </org-table>
              </el-col>
              <!-- 右侧表格 end -->
            </el-row>
          </div>
        </div>
      <!-- 人员结束-->

    <!-- tab切换标签 end -->

  </div>

</template>
<script>

import OrganizationTree from '@/components/biz/tree.vue';
import orgTable from '@/components/biz/orgTable'
export default {
  name: 'chooseOrg',
  data () {
    return {
      searchid:'',
      loading: true,
      // 常用联系人集合
      deletechooseheight:'deletechooseheight',
      deletechoosecontent:'deletechoose-content',
      activeName:'first',
      //组织Id
      currentSelectOrganizationId: '',
      // 是否活动面板
      isActive:false,
      code:'',
      LeaveDimission:false,//离职人员控制按钮
      orgTreeID:''//点击组织树，获取组织id
    };
  },
  components: {
    OrganizationTree,
    orgTable
  },
  props: {
    tags:{
      type: Array,
      default:null
    },
    deleteTags:{
      type: Object,
      default: () => {
        return {
        };
      }
    },
    type:{
      type:String,
      default:''
    },
    mutilselectCheck:{
      type:String,
      default:''
    }
  },
  computed: {
    personTableHeight() {
      return this.tableData.length > 0 ? 185 : 223;
    },
    userTableHeight() {
      return this.userdata.length > 0 ? 185 : 223;
    }
  },
  created () {

  },
  watch:{

  },
  mounted() {

  },
  methods: {
    // 点击组织树节点获取员工
    handleClick (data,node) {
      let _this = this;
      _this.orgTreeID = data.id;
      _this.$refs.orgTable.getOrg(data.id);
    },
    //获取 人员Id
    getPersonEmpId(list){
      let empIds = [];
      for(let item of list){
        let sysid=item.empId;
        if(sysid==undefined && !item.agent){
          empIds.push(item.id);
        }
        if(sysid==undefined && item.agent){
          empIds.push(item.agent);
        }
        else{
          empIds.push(item.empId);
        }

      }
      return empIds;
    },
   //操作tags
    chooseOrgTag(val){
      this.$emit('empSelectTags',val);
    },
  },
};
</script>

<style lang="scss" scoped>
.serchCOL{
  display: flex;
  justify-content: space-between;
  .searchText{
  }
  .searchNext{
    display: flex;
    justify-content: space-between;
  }
}
</style>



