<template>
  <div class="page">
    <el-pagination
      background
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageIndex"
      :page-sizes="pageSizesList"
      :page-size="pageSize"
      @prev-click="firstPage"
      @next-click="tailPage"
      prev-text ='首页'
      next-text='尾页'
      layout="total, prev, pager, next, sizes"
      :total="total" ref='page'>
    </el-pagination>
  </div>
</template>
<script>
export default {
    name: 'page',
    props : {
      //当前页码
      pageIndex: {
        type: Number,
        default:1,
        required: true
      },
      //每页显示条目个数
      pageSize: {
        type: Number,
        default:1,
        required: true
      },
      //总条目数
      total: {
        type: Number,
        default:0,
        required: true
      },
      //每页显示个数选  下拉选择的选项
      pageSizesList: {
        type: Array,
        required: true
      },
    },
    data() {
        return {}
    },
    methods: {
      /**
        * 点击每页条数  pageSize 改变时会触发
        * @param { Number } val [ 每页条数 ]
        */
      handleSizeChange (val) {
        this.$emit('handleSizeChange', val);
      },
      /**
        * 点击页码  currentPage 改变时会触发
        * @param { Number } val [ 当前页 ]
        */
      handleCurrentChange(val) {
        this.$emit('handleCurrentChange', val);
      },
      //点击首页
      firstPage(){
        this.pageIndex = 1
      },
      //点击尾页
      tailPage(){
       this.pageIndex = this.$refs.page.internalPageCount
      }
    }
}
</script>
<style lang="scss" scoped>
  .page {
    margin-top: 10px;
    display: flex;
    .left {
      flex: 1;
      text-align: left;
      span {
        font-size: 14px;
      }
      a {
        padding: 0 2px;
        font-size: 13px;
        height: 28px;
        line-height: 28px;
        box-sizing: border-box;
        margin: 0 5px;
        background-color: #f4f4f5;
        color: #606266;
        min-width: 30px;
        border-radius: 2px;
        display: inline-block;
        text-decoration: none;
        text-align: center;
        cursor: pointer;
        &.on {
          background: #4ca8fc;
          color: #fff;
        }
      }
    }

  }
</style>



