<template>
    <div>
        <el-col :span="8">
            <el-form-item label="所属公司" :title="company">
              <span v-if="isShowStar" style="color: red; font-size: 14px; float: left; position: absolute; top: -5px; left: -10px; z-index: 99">*</span>
                <el-select ref='selectTree' value-key="orgID" clearable @focus="disposeRepetition('输入条件-所属公司')" @clear="clearOrganize" v-model="company" @visible-change="visibleOrganize" placeholder="请选择..." popper-class="tree-list-box">
                    <el-option style="height: auto;padding:0px 0px;" value="orgID" :disabled="true">
                        <div style="height: 100%; width: 100%;">
                            <el-tree :data="organizeList" node-key='orgID' v-loading="loading"
                            highlight-current default-expand-all :expand-on-click-node="false"
                            empty-text='无数据' :props="props" @node-click="selectTreeClick" ref="orgtree"></el-tree>
                        </div>
                    </el-option>
                </el-select>
            </el-form-item>
        </el-col>
        <el-col :span="8">
            <el-form-item label="部门">
                <el-select v-model="department" value-key="id" clearable @focus="disposeRepetition('输入条件-部门')"  @clear="clearDepartment" @change="changeDepartment" @visible-change="visibleDepartment" placeholder="请选择..." popper-class="tree-list-box">
                    <el-option v-for="dept in departmentList" :key="dept.id" :label="dept.name" :value="dept">
                    </el-option>
                </el-select>
            </el-form-item>
        </el-col>
        <el-col :span="8">
            <el-form-item label="项目">
                <el-select v-model="projects" value-key="pc_ID" clearable @focus="disposeRepetition('输入条件-项目')" @clear="clearProject" @change="changeProject" @visible-change="visibleProject" placeholder="请选择..." popper-class="tree-list-box">
                    <el-option v-for="proje in projectList" :key="proje.pc_ID" :label="proje.pc_NAME" :value="proje">
                    </el-option>
                </el-select>
            </el-form-item>
        </el-col>
    </div>
</template>
<script>
import util from "./../../js/util";
import data from "../../data/todoProcess.json";
export default {
  name: "CascaderOrganize",
  props:{
    //defaultFirst 默认 选择第一条数据 的组件
    defaultFirst:{
      type: Array,
      default:undefined
    },
    isShowStar: {
      type: Boolean,
      default: null
    },
    isCheckProcess: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      showStar: false,
      loading:false, //loading
      //tree
      props: {
        label: "orgName",
        value: "orgID",
        children: "m_ChildOrg"
      },
      cascad: {
        organize: {}, //当前选中组织
        department: {}, //部门显示值
        projects: {} //项目显示值
      },
      company: "", //所属公司 显示值
      department:'',//部门显示值
      projects:'',//项目显示值
      organizeList: [], //所属公司 数据
      activeCompany: [], //选中的公司 全 对象
      departmentList: [], //部门数据
      projectList: [], //项目 数据
      cacheMap:{},//缓存数据
    };
  },
  watch: {
    cascad: {
      handler(newval, oldval) {
        this.$emit("changeSetvaule", { data: this.cascad });
      },
      deep: true
    }
  },
  created() {
    this.showStar = this.isShowStar;
    let that = this;
    that.$nextTick(function () {
      //主组件 触发事件
      that.$on('refresh', function () {
        that.refresh()
      })
    });
    if(this.isShowStar){
      this.visibleOrganize();
    }
  },
  methods: {
    /**
     * 公共模块避免埋点名称重复
     */
    disposeRepetition(event) {
      if (!this.$route.name) return
      util.burialPoint(this.$route.name + '-' + event);
    },
    /**
     * 埋点事件
     */
    burialPointEvent(event){
      util.burialPoint(event);
    },
    //刷新
    refresh () {
      this.company = ''      //清空公司选中
      this.department = ''   //部门显示值
      this.projects = ''     //项目显示值
      this.$refs.orgtree.setCurrentKey(null); //取消tree 高亮
      //清空 部门及项目数据
      this.cascad = {
        organize: {}, //当前选中组织
        department: {}, //部门显示值
        projects: {} //项目显示值
      };
      this.departmentList = []; //部门数据
      this.projectList = []; //项目 数据
    },
    //点击 公司
    visibleOrganize(){
      let that = this;
       // 存在公司数据 不做查询
       if(that.organizeList.length==0){
          that.loading = true
          that.getOrganizeList().then(function(data){
            //设置默认当前用户所属组织
            if(data.state && that.defaultFirst && that.defaultFirst.indexOf('organize') > -1){
              let node = that.$refs.orgtree.getNode(data.orgID);
              if(node){
                that.cascad.organize = node.data; //当前选中公司
                that.$refs.orgtree.setCurrentKey(data.orgID);
                that.setOrganize(node);
              }
            }
            that.loading = false
          });
       }
    },
    //点击 公司 tree
    selectTreeClick(data, node) {
      this.department = ''; //部门显示值
      this.projects = '';
      let that = this;
      that.$refs.selectTree.visible = false;
      //点击相同的节点不做任何处理动作
      if(data.orgID == that.cascad.organize.orgID){return}
      that.cascad.organize = data; //当前选中公司
      //清空 部门及项目数据
      that.departmentList = []; //部门数据
      that.cascad.department = {};
      that.projectList = []; //项目 数据
      that.cascad.projects = {};
      that.setOrganize(node); //设置 下拉 选中 数据
    },
    //清空公司选中
    clearOrganize(){
      let that = this
      //清空 当前选中公司数据
      that.refresh()
      //重置 tree 选中状态
      let node = that.$refs.orgtree.getCurrentNode()
      if(node){
        that.$refs.orgtree.setCurrentKey(null)
      }
    },
    //设置 下拉 选中 数据
    setOrganize(node) {
      let that = this;
      let companyName = []; //组织全路径
      that.activeCompany = []; //组织全 对象
      that.getPrarentNode(node, companyName); // 获取当前选中的组织
      that.company = companyName.reverse().join("/")
      that.company = that.company.substr(1, that.company.length)
    },
    //获取所有上级节点
    getPrarentNode(pNode, companyName) {
      if (pNode.data != undefined) {
        this.activeCompany.push(pNode.data);
        companyName.push(pNode.data.orgName);
      }
      if (pNode.parent != undefined) {
        this.getPrarentNode(pNode.parent, companyName);
      }
    },
    // 获取公司数据
    getOrganizeList() {
      let that = this;
      let obj = {};
      if (this.isCheckProcess === '0') {
        obj.isSelect = '0'
      }
      return new Promise((resolve, reject) => {
        that.$axios.post("/api/companytree/page", obj).then(res => {
          if (res.data.companyTreeList.length > 0) {
            that.organizeList = res.data.companyTreeList
            let orgID = that.organizeList[0].orgID
            resolve({state:true,orgID});
          }else{
            resolve({state:true});
          }
        }).catch(err => {
          reject(`获取组织数据失败!ERROR MESSAGE:${err}`)
        })
      })
    },
    //通过公司获取部门
    getdepartmentList(orgID) {
      let that = this;
      that.$axios.post("/api/department/page", {orgID}).then(res => {
          that.departmentList = res.data.departmentList;
          //设置数据缓存
          that.setCacheMap(that.departmentList,orgID,'department')
      }).catch(err => {
        that.$message.error(`获取部门数据失败!ERROR MESSAGE:${err}`);
      });
    },
    //选中部门
    changeDepartment(depart) {
      console.log('选中部门',depart);
      this.cascad.department = depart
    },
    //清空部门选中
    clearDepartment(){},
    //点击部门
    visibleDepartment(val){
        let that = this;
        if(val && that.cascad.organize.orgID){
          //有缓存数据
          if(that.cacheMap[that.cascad.organize.orgID] && that.cacheMap[that.cascad.organize.orgID].department){
            that.departmentList = that.cacheMap[that.cascad.organize.orgID].department
            return
          }
          that.getdepartmentList(that.cascad.organize.orgID)
        }
    },
    //通过公司Id获取项目
    getprojectList(orgID) {
      let that = this;
      that.$axios.post("/api/project/page", {orgID}).then(res => {
        that.projectList = res.data.projectList;
        //设置数据缓存
        that.setCacheMap(that.projectList,orgID,'project')
      }).catch(err => {
        that.$message.error(`获取项目数据失败!ERROR MESSAGE:${err}`);
      });
    },
    //选中项目
    changeProject(project){
      console.log('选中项目',project);
      this.cascad.projects = project
    },
    //清空项目选中
    clearProject(){},
    //点击项目
    visibleProject(val){
      let that = this;
      if(val && that.cascad.organize.orgID){
         //有缓存数据
        if(that.cacheMap[that.cascad.organize.orgID] && that.cacheMap[that.cascad.organize.orgID].project){
          that.projectList = that.cacheMap[that.cascad.organize.orgID].project
          return
        }
        that.getprojectList( that.cascad.organize.orgID)
      }
    },
    /**
      * 设置数据缓存
      * @param {Array} list 需要缓存的数据
      * @param {String} orgID 当前选择的公司Id
      * @param {String} type  当前缓存的类型（组织、项目）
      */
    setCacheMap(list,orgID,type){
      let that = this
      //不存在当前公司对象 创建
      if(!that.cacheMap[orgID]){
        that.cacheMap[orgID] = {}
      }
      that.cacheMap[orgID][type] = list
    }
  }
};
</script>


<style scoped lang="scss">
.el-input :-ms-input-placeholder.el-input__inner{
  color: #c0c4cc !important;
}
</style>



