<template>
  <div class="originater_dialog project_dialog">
    <div  :class="[closeShow?'pc-candidates':'pc-candidates pc-background']">
      <div class="dialog_content">
        <el-main style="position:relative;">
          <div class="top" v-show="!closeShow">组织选择<span style="font-size: 30px;cursor: pointer;float:right;padding-right:5px" @click="close">×</span></div>
          <el-tabs type="card">
            <el-tab-pane class="candidates-recently">
              <span slot="label"><i class="iconfont icon-todos"></i>&nbsp;组织</span>
              <div class="title  title-border">
                <!--** 面包屑 begin -->

                  <el-breadcrumb separator-class="el-icon-arrow-right">
                    <el-breadcrumb-item>组织架构</el-breadcrumb-item>
                    <!-- tooltip element组件提示效果 -->
                    <!-- <el-tooltip class="item" effect="dark" v-if="bread.length > 0" :content="tooltipOrganName"  placement="top-start"> -->
                        <el-breadcrumb-item :title="tooltipOrganName">{{bread}}</el-breadcrumb-item>
                     <!-- </el-tooltip> -->
                  </el-breadcrumb>

                 <!--** 面包屑 end -->
                <span class="rightInput">
                  <!-- 搜索框注销 -->
                    <el-input 
                      placeholder="请输入部门关键字"
                      prefix-icon="el-icon-search"
                      v-model="treeSearch"
                      size="mini" >
                    </el-input>
                  </span>
              </div>
              <div class="content clearfix">
                <div class="content-left">
                  <!-- <div class="title"> -->
                    <!-- 搜索  begin -->
                    <!-- <el-input
                      placeholder="请输入部门关键字"
                      prefix-icon="el-icon-search"
                      v-model="treeSearch"
                      size="mini" >
                    </el-input> -->
                    <!-- 搜索  end -->
                  <!-- </div> -->
                  <el-tree
                    :data="organizationData"
                    node-key="orgID"
                    :highlight-current="true"
                    :props="defaultProps"
                    :default-expanded-keys="defaultKeys"
                    :default-checked-keys="defaultKeys"
                    @node-click="handleNodeClick"
                    ref="tree2"
                    :load="handleNodeload"
                    lazy>
                  </el-tree>

                </div>
                <div class="content-right">
                  <el-checkbox :indeterminate="isIndeterminate"  v-if="!oneCheck" v-model="checkAll" @change="handleCheckAllChange" style="padding-top:6px;">全选
                  </el-checkbox>

                  <!-- 单选时 结构 -->
                  <el-checkbox-group  class="theRadio"  v-if="oneCheck" v-model="checkedCitie" @change="handleCheckedCitiesChange">
                    <el-checkbox-button v-for="item in cities" :label="item" :key="item.orgID">
                      <div class="media">
                        <div class="media-body" @click="clickDepartment(item)"  @dblclick="dblclickCheckedCitie(item)">
                          <!-- <i class="companyIcon"></i><h4 class="media-heading">{{item.orgName}}</h4> -->
                          <h4 class="media-heading iconfont icon-company">{{item.orgName}}</h4>
                        </div>
                      </div>
                    </el-checkbox-button>
                  </el-checkbox-group>

                  <!-- 多选时 结构 -->
                  <el-checkbox-group class="mut-check" v-else v-model="checkedCitie" @change="handleCheckedCitiesChange">
                    <el-checkbox v-for="(item, index) in cities" :label="item" :key="index" :checked="item.checked">
                      <div class="media">
                        <div class="media-body mut-media-body">
                          <h4 class="media-heading iconfont icon-company">{{item.orgName}}</h4>
                        </div>
                      </div>
                    </el-checkbox>
                  </el-checkbox-group>
                </div>
              <!-- 设置多选时 显示选择的数据 -->
              </div>

                <div class="addrecently"  v-bind:class="{ pullup: isActive }" >
                    <div class="media">
                      <div class="pull" @click="pullBtnHeightChange"></div>
                      <ul class="clearfix">
                        <!-- 清空按钮 -->
                        <div class="clearBtn"  v-show="checkedCitie.length>0"  @click="clearProject"></div>
                        <li v-for="(project, index) in checkedCitie" :key="project.orgID">
                          <div class="media-body">
                            <i class="el-icon-circle-close" @click="deleteProject(project, index)"></i>
                            <span class="media-heading">{{project.orgName}}</span>
                          </div>
                        </li>
                      </ul>
                    </div>
                </div>

              <div class="addrecently-footer">
                <el-button type="primary" @click="sureSubmit" icon="el-icon-circle-check-outline" size="mini">确定</el-button>
                <el-button type="primary" @click="close" icon="el-icon-circle-close-outline" size="mini">取消</el-button>
              </div>
            </el-tab-pane>
          </el-tabs>
        </el-main>
      </div>
    </div>
  </div>
</template>

<script>

  export default {
    name: "selectedPersonnel",
    props:{
      childDataUrl:{
        type : Object,
        required : true
      },
      closeShow: false,
      oneCheck: {
        type: Boolean,
        default: true
      }
    },
    mounted() {
      // this.getTree();
      if (this.childDataUrl.chooseProject && this.childDataUrl.chooseProject.item && this.childDataUrl.chooseProject.last) {
        this.treeNode = this.childDataUrl.chooseProject.item;
        this.last = this.childDataUrl.chooseProject.last;
        this.checkedCitie.push(this.treeNode);
      }
    },

    data() {
      return {
        'organName':'',//组织结构全名
        mianbao: [],
        value: '',
        options: [],
        bread: '',
        fullSearch: '',//搜索项目
        treeSearch:'',//搜索tree
        checkAll: false,
        checkedCitie: [],//选中的数据
        cities: [],
        isIndeterminate: false,
        organizationData: [],//树数据
        oldtime:0,
        defaultProps: {
          children: 'm_ChildOrg',
          label: 'orgName',
          isLeaf: function(data, node) {
            if (data && data.isParent > 0) {
              return false;
            } else {
              return true;
            }
          }
        },
        last: {},//列表选中数据
        treeNode: {},//tree选中数据
        isActive: false,
        tooltipOrganName:'',//组织全名显示tip数据
        defaultKeys: [],
        isSeach:false,
        treeQuery:false
      };
    },
    watch: {
      treeSearch(val) {
        var that = this;
        that.treeQuery = true;
        that.queryName = val;
        this.loading = true;
        if (this.isSeach || that.queryName.trim() =='') {
          return;
        }
        this.isSeach = true;
        setTimeout(() => {
          that.isSeach = false;
          that.searchTree(that.queryName);
        }, 2000);
      }
    },
    methods: {
      searchTree(orgName){
        let isFirst = null;
        if(this.childDataUrl.isFirst){
          isFirst = this.childDataUrl.isFirst
        }
        this.$axios.post(`/api/${this.childDataUrl.getTree}`, {'orgName':orgName,isFirst}).then((result) => {
          this.organizationData = this.toTree(result.data.result);
        }).catch(err => {
          this.$message.error(`查询部门组织数据失败!ERROR MESSAGE:${err}`);
        });
      },
      /**
       * 控制弹窗底部已选区域展开收起
       * style by pcCandidates.scss
       */
      pullBtnHeightChange () {
        this.isActive = !this.isActive;
      },
      /**
       * 处理部门全名称
       */
      disposeOrganName(orgNames){
        var organName= '';
        for(let i = 0; i < orgNames.length;i++) {
          var orgName = orgNames[i];
          organName += `${orgName.text}>`;
        }
        organName =  organName.substr(0, organName.length - 1);
        this.tooltipOrganName = organName;
        if(organName.length > 30){
           this.bread = organName.substr(0,30)+'...';
        }else{
          this.bread = organName;
        }
        return organName;
      },
      handleNodeClick (data,node) {
        var myDate = new Date();
        var now = myDate.getTime();
        if (this.oldtime) {
          var over = now - this.oldtime;
          if (over < 200) {
            this.treeNode.orgFullName = node.data.orgFullName;
            this.treeNode.orgID = node.data.orgID;
            this.treeNode.orgName = node.data.orgName;
            this.treeNode.parentOrgID = node.data.parentOrgID;
            this.clickDepartment(this.treeNode);
            this.$emit('sureSubmit', {type:'project',item: this.treeNode, last: this.last});
            return;
          }
        }
        this.oldtime = now;

        if (!node.isLeaf) {
          this.cities = [];
          for(var i=0; node.childDatas && i < node.childDatas.length; i++) {
            this.cities.push(node.childDatas[i]);
          }
          // this.mianbao=[];
          // this.getPrarentNode(node);
        } else {
          this.cities = [];
          var temp = {};
          temp.isParent = data.isParent;
          temp.orgFullName = data.orgFullName;
          temp.orgID = data.orgID;
          temp.orgName = data.orgName;
          temp.parentOrgID = data.parentOrgID;
          this.cities.push(temp);
        }
        for(var i=0; i < this.cities.length; i++) {
          var bool = false;
          for (var j=0; j < this.checkedCitie.length; j++) {
            if (this.cities[i].orgID === this.checkedCitie[j].orgID) {
              bool = true;
              break;
            }
          }
          if (bool) {
            this.cities[i].checked = true;
          } else {
            this.cities[i].checked = false;
          }
        }
        this.mianbao = [];
        this.getPrarentNode(node);
        this.disposeOrganName(this.mianbao)
      },
      handleNodeload (node, resolve) {
        if (!node.data || node.data.length == 0) {
          this.getTree(resolve);//查询部门组织树的数据 查询在职
        } else {
           this.mianbao=[];
           if (node.data.m_ChildOrg && node.data.m_ChildOrg.length > 0) {
             var childs = [];
             node.childDatas = [];
             for (var i=0; i < node.data.m_ChildOrg.length; i++) {
               childs.push(node.data.m_ChildOrg[i]);
               node.childDatas.push(node.data.m_ChildOrg[i]);
             }
             delete node.data.m_ChildOrg;
             resolve(childs);
           } else {
             this.$axios.post(`/api/${this.childDataUrl.getTree}`, {'orgDepId':node.data.orgID}).then((result) => {
               if(result.status === 200) {
                 node.childDatas = [];
                 this.cities = [];
                 for (var i = 0; i < result.data.result.length; i++) {
                   node.childDatas.push(result.data.result[i]);
                   this.cities.push(result.data.result[i]);
                 }
                 resolve(result.data.result);
               } else {
                 this.$message.error("请求失败！");
               }
             }).catch(err => {
               that.$message.error(`查询数据失败!ERROR MESSAGE:${err}`);
             });
           }
        }
      },
       /**
       * 查询部门组织树的数据 查询在职
       * getTree
       */
      getTree(resolve){
        let isFirst = null;
        if(this.childDataUrl.isFirst){
          isFirst = this.childDataUrl.isFirst
        }
        this.$axios.post(`/api/${this.childDataUrl.getTree}`, {'orgDepId':'A5A40ECE-0DBD-4F2B-89E8-6BA91B91EBF4',isFirst}).then((result) => {
          if(result.status === 200) {
            this.organizationData = this.toTree(result.data.result);
            if (resolve) {
              resolve(this.organizationData);
              this.defaultKeys[0]=this.organizationData[0].orgID;
            }
          } else {
            this.$message.error("请求失败！");
          }
        }).catch(err => {
          this.$message.error(`查询部门组织数据失败!ERROR MESSAGE:${err}`);
        });
      },
      // 确认
      sureSubmit () {
        if (!this.checkedCitie || this.checkedCitie.length === 0) {
          this.$message.warning("请选择数据!");
          return;
        }
        if (this.oneCheck) {
          this.$emit('sureSubmit', {type:'project',item: this.treeNode, last: this.last});
        } else {
          this.$emit('sureSubmit', {type:'project',items: this.checkedCitie});
        }

      },
      //关闭弹窗
      close() {
       this.$emit('hideComponent');
      },
      //回车  搜索项目
      searchProject(){
        //TODO :
      },
      //转换tree数据格式
       toTree(data) {
         if(data==null || data.length==0) return [];
        data.forEach(function (item) {
          delete item.m_ChildOrg;
        });

        var map = {};
        data.forEach(function (item) {
          map[item.orgID] = item;
        });

        var val = [];
        data.forEach(function (item) {
          var parent = map[item.parentOrgID];
          if (parent) {
            (parent.m_ChildOrg || ( parent.m_ChildOrg = [] )).push(item);
          } else {
            val.push(item);
          }
        });
        return val;
      },
     /**
      * 当前选中节点变化时触发的事件
      */
      checkChangeNode(data,isChecked,childIsChecked){
      },
      // 全选/反选
      handleCheckAllChange(val) {
        if(val){
          for (var i = 0; i < this.cities.length; i++) {
            var bool = false;
            for (var j = 0; j < this.checkedCitie.length; j++) {
              if (this.checkedCitie[j].orgID === this.cities[i].orgID) {
                bool = true;
              }
            }
            if (!bool) {
              this.checkedCitie.push(this.cities[i]);
            }
          }
           this.isIndeterminate = true;
         }else{
          for (var i = 0; i < this.cities.length; i++) {
            var bool = false;
            for (var j = 0; j < this.checkedCitie.length; j++) {
              if (this.checkedCitie[j].orgID === this.cities[i].orgID) {
                bool = true;
                break;
              }
            }
            if (bool) {
              this.checkedCitie.splice(j,1)
            }
          }
          this.isIndeterminate = false;
         }
      },
      /**
      * 右侧列表勾选, 多选走此方法
      */
      handleCheckedCitiesChange(value) {
        this.checkedCitie = value;
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.cities.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
      },
      /**
       * 点击右侧列表, 单选走此方法
       */
      clickDepartment(item) {
        let that = this;
        this.checkedCitie.splice(0,this.checkedCitie.length);
        this.checkedCitie.push(item);
          //获取tree Node
        let node =  this.$refs.tree2.getNode(item.orgID);
        this.mianbao = [];
        this.getPrarentNode(node);
          that.organName='';
          //获取完整的组织结构名称
          for(let it of that.mianbao){
            that.organName += `${it.text}>`
          }
          that.organName += `${item.orgName}`;
          that.treeNode = item; //tree选中数据
          that.last.lastNode='';
          that.last.lastId ='';
          that.mianbao.forEach(function(it){
              that.last.lastNode += `${it.text}-`;
              that.last.lastId = it.petId;
          });
          that.last.lastNode =  that.last.lastNode.substr(0, that.last.lastNode.length-1);
          // that.$emit('sureSubmit', {type:'project',item: item, last: that.last});
      },
      //单选 双击右侧列表直接选中赋值
      dblclickCheckedCitie(item){
          this.clickDepartment(item);
          this.$emit('sureSubmit', {type:'project',item:item, last: this.last});
      },
      // 删除所选组织
      deleteProject (data, index) {
        this.checkedCitie.splice(index,1);
      },
      // 清空所有选择
      clearProject () {
        this.checkedCitie.splice(0,this.checkedCitie.length);
        this.checkAll = false;
        this.isIndeterminate =false;
      },
      //获取所有上级节点
      getPrarentNode(pNode) {
        if (pNode.data.orgName != undefined) {
          this.mianbao.unshift({text: `${pNode.data.orgName}`,petId:`${pNode.data.orgID}`});
        }
         if (pNode.parent != undefined) {
            this.getPrarentNode(pNode.parent);
          }
      },
      //获取子节点
      getChild(cNode) {
        if (typeof cNode.childNodes === 'undefined' || cNode.childNodes.length === 0) {
          this.last = {
            lastNode: cNode.data.orgName,
            lastId: cNode.data.orgID
          }
        }
      }
    }
  }
</script>


