<template>
  <div class="choosePerson">
    <!-- 人员开始-->
    <div class="choose-person-container" >
      <div class="content-body">
        <el-row>
          <process-tree @node-click="handleClick"
                        :parmDialog="parmDialog"
                        :deleteTags="deleteTags"
                        :ProcessType="ProcessType"
                        ref="chooseTree"
                        :tags="tags"
                        :Visible="Visible"
                        class="choosePersonTable"
                        :mutilselectCheck="mutilselectCheck"
                        @ProcessCheck="ProcessCheck">
          </process-tree>
        </el-row>
      </div>
    </div>
    <!-- 人员结束-->

    <!-- tab切换标签 end -->

  </div>

</template>
<script>

  import processTree from '@/components/biz/processTree.vue';
  export default {
    name: 'ChoosepersonNew',
    data () {
      return {
        searchid:'',
        loading: true,
        // 常用联系人集合
        deletechooseheight:'deletechooseheight',
        deletechoosecontent:'deletechoose-content',
        //组织Id
        currentSelectOrganizationId: '',
        code:'',
      };
    },
    components: {
      processTree,
    },
    props: {
      tags:{
        type: Array,
        default:null
      },
      deleteTags:{
        type: Object,
        default: () => {
          return {
          };
        }
      },
      type:{
        type:String,
        default:''
      },
      mutilselectCheck:{
        type:String,
        default:''
      },
      parmDialog: {
        type: Object,
        default: ()=>{
          return new Object()
        }
      },
      ProcessType:{
        type:String,
        default:''
      },
      Visible:{
        type:Boolean,
        default:false
      }
    },
    computed: {
      personTableHeight() {
        return this.tableData.length > 0 ? 185 : 223;
      },
      userTableHeight() {
        return this.userdata.length > 0 ? 185 : 223;
      }
    },
    created () {

    },
    watch:{

    },
    mounted() {
      this.$nextTick(()=>{

//      this.$refs.chooseTree.getPerson()
      });
    },
    methods: {
      // 点击组织树节点获取员工
      handleClick (data,node) {

      },
      //获取 人员Id
      getPersonEmpId(list){
        let empIds = [];
        for(let item of list){
          let sysid=item.empId;
          if(sysid==undefined && !item.agent){
            empIds.push(item.id);
          }
          if(sysid==undefined && item.agent){
            empIds.push(item.agent);
          }
          else{
            empIds.push(item.empId);
          }

        }
        return empIds;
      },
      //操作tags
      ProcessCheck(val){
        this.$emit('empSelectTags',val);
      },
    },
  };
</script>

<style lang="scss" scoped>
  .serchCOL{
    display: flex;
    justify-content: space-between;
    .searchText{
    }
    .searchNext{
      display: flex;
      justify-content: space-between;
    }
  }
</style>



