<template>
  <div class="flow_chart">
    <div class="pc-candidates pc-background">
      <div class="dialog_content">
        <div class="top">流程图<span style="cursor:pointer; font-size: 30px;" @click="close">×</span></div>
        <div class="initProcess">
          <el-tabs type="border-card" v-model="activeName" @tab-click="handleClick"
                   v-loading="loading"
                   element-loading-text="拼命加载中"
                   element-loading-background="rgba(255, 255, 255, 0.8)">
            <el-tab-pane calss="steps" name="flowStep">
              <span slot="label" name="step"><i class="iconfont icon-processsteps"></i> 流程步骤</span>
              <div class="content">
                <div class="content-top">
                  <h2 class="name">流程模板：<span>{{processtemplatename}}</span></h2>
                </div>
                <div class="scrollDiv">
                  <div class="clearfix" v-for="(item, index) in templateStep" :key="index">
                    <el-table
                      :data="item.stepData"
                      border
                      style="width: 100%">
                      <el-table-column
                        :label="(index+1).toString()"
                        align="center"
                        width="50">
                      </el-table-column>
                      <el-table-column
                        prop="username"
                        :label="item.activityname"
                        align="center"
                        :render-header="labelHead"
                        width="180">
                      </el-table-column>
                      <el-table-column
                        prop="finishdate"
                        label="填单时间"
                        align="center">

                      </el-table-column>
                      <el-table-column
                        label="所用时间"
                        align="center"
                        prop="useTime"
                      >
                        <template slot-scope="scope">{{scope.row.useTime}}</template>
                      </el-table-column>
                    </el-table>
                  </div>
                </div>
              </div>

            </el-tab-pane>
            <el-tab-pane name="flowChart">
              <span slot="label" @click="burialPointEvent('列表-流程图-流程图')"><i class="iconfont icon-flowchart"></i> 流程图</span>
              <div>
              <!-- finish-status="success"  -->
                <el-steps  :align-center="true">
                   <div class="step-linerow" v-for="(activityRemark,index) in flowChartdata" :key="index">
                    <el-step :status="step.status" v-for="step in activityRemark" :key="step.activityid"
                    :title="step.activityname.length>27?`${step.activityname.substr(0,25)}...`:step.activityname"></el-step>
                  </div>
                </el-steps>
              </div>

            </el-tab-pane>
            <el-tab-pane name="flowLog">
              <span slot="label" @click="burialPointEvent('列表-流程图-操作日志')"><i class="iconfont icon-operationlog"></i> 操作日志</span>
              <template>
                <el-table
                  :data="logData"
                  border
                  style="width: 100%">
                  <el-table-column
                    label=" "
                    type="index"
                    header-align="center"
                    width="50">
                  </el-table-column>
                  <el-table-column
                    prop="username"
                    label="操作人"
                    header-align="center"
                    win-width="160">
                  </el-table-column>
                  <el-table-column
                    prop="timestamp"
                    label="时间"
                    header-align="center"
                    win-width="140">
                    <template slot-scope="scope">{{scope.row.timestamp | moment('YYYY-MM-DD HH:mm:ss')}}</template>
                  </el-table-column>
                  <el-table-column
                    prop="message"
                    header-align="center"
                    width="135"
                    label="操作结果">
                  </el-table-column>
                </el-table>
              </template>
            </el-tab-pane>
          </el-tabs>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import util from '../../js/util'
  import moment from 'moment'
  import loading from '../../plugins/loading/loading';
  export default {
    name: '',
    props: {
      rowData: {
        type: Object
      }
    },
    data () {
      return {
        activeName:'flowStep',
        //流程步骤 数据
        'templateStep':[],
        // 日志数据
        logData: [],
        //流程图数据
        flowChartdata:[],
        //流程模板名称
        processtemplatename:'',
        //当前流程状态
        activeIndex:0,
        //loading
        loading:false,
      }
    },
    mounted () {
      this.burialPointEvent('列表-流程图图标');
      this.burialPointEvent('列表-流程步骤');
      // 流程步骤 后台数据有问题
      this.getFlowStep();
      this.closeProcessImg();
    },
    methods: {
      /**
       * 埋点事件
       */
      burialPointEvent(event){
        util.burialPoint(this.$route.name + '-' + event);
      },
      //处理 username 长度超出 ...
      labelHead(h,{column,index}){
        return(<span title={column.label}>{(column.label!=null && column.label.length>12)?column.label.substr(0,12)+"...":column.label}</span>);
      },
      //关闭弹窗
      close(){
        this.$emit('hideComponent')
      },
      // 点击空白处 关闭弹窗
      closeProcessImg() {
        let node = document.getElementsByClassName('pc-candidates')[0];
        node.addEventListener('click', (e) => {
          if(e.target.classList[0] === 'pc-candidates'){
            this.$emit('hideComponent');
          }
        })
      },
      //切换Tab页
      handleClick (tab, event) {
        let that = this
        switch(tab.name){
          case "flowStep":break;
          case "flowChart":
            if(!that.flowChartdata || that.flowChartdata.length ==0){
              //  流程图
              that.getFlowChart();
            }
          break;
          case "flowLog":
            if(!that.logData || that.logData.length ==0){
              // 流程日志
              this.getFlowLog();
            }
          break;
        }
      },
      // 流程步骤
      getFlowStep () {
        let that = this
        var procinstid = that.rowData.procInstID;
        var procinstno = that.rowData.procInstNo;
        var stepUrl = '/api/vkProcess/step';
        var obj = {
          procinstid: procinstid
        }
        that.loading = true
        that.$axios.post(stepUrl, obj).then((res) => {
          let data = res.data;
          that.processtemplatename = data.processtemplatename;//模板名称
          for(let temp of data.result){
            let finishdate = temp.finishdate;//结束时间
            let startdate = temp.startdate;//开始时间
            let useTime ='';
            if(that.CheckDateTime(startdate) && that.CheckDateTime(finishdate)){
              useTime = that.returnatetime(startdate,finishdate);
            }
            let te = {
              'activityname':temp.activityname,
              'stepData':[{'username':temp.username,'finishdate':finishdate,'useTime': useTime,}]
            }
            that.templateStep.push(te);
          }
          that.loading = false
        }).catch(err => {
           that.loading = false
          that.$message.error('获取流程步骤失败！'+err);
        })
      },
      // 计算时差
      returnatetime(startdate,finishdate){
        startdate=startdate.replace(new RegExp(/-/gm) ,"/");
        finishdate=finishdate.replace(new RegExp(/-/gm) ,"/");
        var date1=new Date(startdate);  //开始时间
        var date2=new Date(finishdate);    //结束时间
        var date3=date2.getTime()-date1.getTime()  //时间差的毫秒数
        //计算出相差天数
        var days=Math.floor(date3/(24*3600*1000))

        //计算出小时数
        var leave1=date3%(24*3600*1000)    //计算天数后剩余的毫秒数
        var hours=Math.floor(leave1/(3600*1000))
        //计算相差分钟数
        var leave2=leave1%(3600*1000)        //计算小时数后剩余的毫秒数
        var minutes=Math.floor(leave2/(60*1000))
        //计算相差秒数
        var leave3=leave2%(60*1000)      //计算分钟数后剩余的毫秒数
        var seconds=Math.round(leave3/1000)
        return days+"天 "+hours+"小时 "+minutes+" 分钟"+seconds+" 秒";
      },
      //检查是否为日期时间
      CheckDateTime(str){
        var reg = /^(\d+)-(\d{1,2})-(\d{1,2}) (\d{1,2}):(\d{1,2}):(\d{1,2})$/;
        var r = str.match(reg);
        if(r==null)return false;
        r[2]=r[2]-1;
        var d= new Date(r[1], r[2],r[3], r[4],r[5], r[6]);
        if(d.getFullYear()!=r[1])return false;
        if(d.getMonth()!=r[2])return false;
        if(d.getDate()!=r[3])return false;
        if(d.getHours()!=r[4])return false;
        if(d.getMinutes()!=r[5])return false;
        if(d.getSeconds()!=r[6])return false;
        return true;
      },
      // 流程日志
      getFlowLog () {
        let that = this
        var procinstid = this.rowData.procInstID;
        var logUrl = '/api/vkProcess/log';
        var obj = {
          procinstid: procinstid
        }
        that.loading = true
        that.$axios.post(logUrl, obj).then((res) => {
          that.logData = res.data.result;
          that.loading = false
        }).catch(err => {
          that.loading = false
          that.$message.error('获取流程日志失败！'+err);
        })
      },
      // 获取流程图
      getFlowChart () {
        let that = this
        var procinstid = that.rowData.procInstID;
        that.loading = true
        // 流程图
        that.$axios.post('/api/vkProcess/img', {procinstid}).then((res) => {
          let data  = res.data.result
          let stepObj = data.stepObj
          let stepName = data.stepName
          let flowChartdata = data.activity
          that.setStepValue(flowChartdata,stepObj,stepName)
          this.flowChartdata = that.split(flowChartdata)
          that.loading = false
        }).catch(err => {
          that.loading = false
          this.$message.error('获取流程图失败！'+err);
        })
      },
      //处理流程图 数据 显示状态
      setStepValue(list,stepObj,stepName){
         //1、stepObj stepName 都为null 表示已通过  已结束的
        if(!stepObj && !stepName){
           for(let item of list){
            item.status = 'finish' //设置状态完成
          }
        }
         //2、stepObj stepName 都为不 null 表示审批中
        if(stepObj && stepName){
          for(let item of list){
            if(Number(item.activityorder) < Number(stepObj.activityorder)){
              item.activityname +='(已完成)'
              item.status = 'finish'  //设置状态 完成
            }else if(Number(item.activityorder) == Number(stepObj.activityorder)){
              item.activityname +='(审批中)'
              item.status = 'process'  //设置状态 进行中
            }else if(Number(item.activityorder) > Number(stepObj.activityorder)){
              item.activityname +='(未审批)'
              item.status = 'wait'  //设置状态 未开始
            }
          }
          this.activeIndex = Number(stepObj.activityorder)
        }
        //3、stepObj 为null stepName='草稿'   表示 当前步骤处于退回修改中
        if(!stepObj && stepName){
          for(let item of list){
            item.activityname +='(未审批)'
            item.status = 'wait'  //设置状态 未开始
          }
        }
        list.unshift({'activityid':'start_','activityname':'开始','status':"finish"})
      },
      //处理 list数据 转二维数组
      split(array) {
        let chunks = [];
        let count = Math.ceil(array.length / 5);
        while (count > 0) {
          chunks.push(array.slice((count - 1) * 5, count * 5));
          count--;
        }
        chunks = chunks.reverse();
        return chunks;
      }
    }
  }
</script>

<style scoped lang="scss">

  .flow_chart {
    .pc-candidates{
      position: fixed;
      left: 0;
      top:0;
      width: 100%;
      height: 100%;
      z-index: 999;
      .dialog_content{
        position: absolute;
        top:50%;
        left: 50%;
        transform:translate(-50%,-50%);
        border-radius: 15px;
        width: 680px;
        .top{
          display: flex;
          justify-content: space-between;
          font-size: 16px;
          color: #fff;
          height: 50px;
          line-height: 50px;
          background: #38acff;
          background: -webkit-linear-gradient(right, #3dd7fe , #3195f8); /* Safari 5.1 - 6.0 */
          background: -o-linear-gradient(left, #3dd7fe , #3195f8); /* Opera 11.1 - 12.0 */
          background: -moz-linear-gradient(left, #3dd7fe , #3195f8); /* Firefox 3.6 - 15 */
          background: linear-gradient(to left,  #3dd7fe , #3195f8); /* 标准的语法 */
          padding: 0 20px;
          border-top-left-radius: 10px;
          border-top-right-radius: 10px;
        }
      }
    }
    .initProcess {
      .flowImg {
        width: 256px;
        height: 458px;
      }
    }
    .addrecently-footer{
      height: 30px;
      padding:10px;
      text-align: center;
      position: absolute;
      bottom: 0px;
      left: 38%;
    }
  }
</style>




