<!--左侧树结构-->
<template>
  <div>
    <el-tree
      ref="customRoleTree"
      :node-key="nodKey"
      :props="defaultProps"
      :data="organizationList"
      :highlight-current="highlightCurrent"
      :check-on-click-node="checkOnClickNode"
      :expand-on-click-node="expandOnClickNode"
      :auto-expand-parent="autoExpandParent"
      :load="loadNodeData"
      :loading="loading"
      :height="personTableHeight"
      lazy
      @node-click="nodeClick"
      @current-change="currentChange"
      @check-change="checkChange"
      @check="check">
    </el-tree>
  </div>
</template>
<script>
  // `debounce` 是一个通过 Lodash 限制操作频率的函数。
  import {debounce} from 'lodash';
  import { baseUrl } from '../../../static/commonApi';
  export default {
    name: 'customRoleTree',
    props: {
      // tree 主键 key
      nodKey:{
        type:String,
        default:'id',
        require:false
      },
      //是否高亮显示
      highlightCurrent:{
        require: false,
        type: Boolean,
        default: true
      },
      //是否在点击节点选中该节点
      checkOnClickNode:{
        type:Boolean,
        require:false,
        default:true
      },
      //是否在点击节点的时候展开或者收缩节点
      expandOnClickNode:{
        type:Boolean,
        require:false,
        default:true
      },
      //是否在第一次展开某个树节点后才渲染其子节点
      renderexpand:{
        type:Boolean,
        require:false,
        default:true,
      },
      //展开子节点的时候是否自动展开父节点
      autoExpandParent:{
        type:Boolean,
        require:false,
        default:true,
      },

      // 懒加载是否用 IdPath做查询条件
      depthIdPath:{
        type:Boolean,
        require:false,
        default:false,
      },
      // 是否在页面加载时主动获取数据
      isMountedGetData:{
        type:Boolean,
        require:false,
        default:true,
      },
      // 默认根节点数据
      defaultRootNode:{
        type:Object,
        require:false,
        default:null,
      },
      // 默认显示名称字段 扩展显示名称字段
      defaultPropLabel:{
        type:String,
        require:false,
        default:'displayName',
      },
      // 默认排序字段 扩展排序字段
      defaultSort:{
        type:String,
        require:false,
        default:'orderIndex',
      },

    },
    data () {
      return {
        organizationList:[],
        //默认树形
        // tree 默认配置
        defaultProps:{
          label:'title',
          children: 'children',
          // 设置是否是叶子节点 显示头部小图标
          isLeaf:'isLast',
        },
        // 树加载loadding
        loading:false,
        // 当前点击 tree 的 node
        currentNode:null,
        //第一次加载树标识
        isFristLoadTree:true,
        userPickUrl:baseUrl //用户控件Url
      };
    },
    created: function (node) {
      var _this = this;
      // `debounce` 是一个通过 Lodash 限制操作频率的函数。
      _this.debouncedSearchTree = debounce(_this.getTreeList,null);
    },
    mounted() {
      if( this.isMountedGetData){
        this.debouncedSearchTree(null);
      }
    },
    methods: {
      /**
       * tree 懒加载 触发事件
       * @param {Object} node tree 的节点Node
       * @param {Function} resolve  懒加载回调函数
       */
      loadNodeData(node, resolve) {
        if (node.level === 0) {
          this.isFristLoadTree = false;
          return;
        }
        else {
          this.debouncedSearchTree(node,resolve);
          return;
        }
      },
      /**
       * 获取组织数据
       * @param {String} code 当前选择组织架构的code
       * @param {String} aId  当前查询公司Id
       * @param {Function} resolve  懒加载回调函数
       * @param {String} currentKey  设置默认选中的节点Id
       * @param {Boolean} first  是否初次加载数据
       */
      getTreeList(node,resolve){
        let _this = this;
        //初始状态下
        if(node === null){
          var data = null
        }
        //拥有二级菜单
        else {
          if(!node.data.isLast) {
            var data = {
              ParentID: node.data.id,
              context: node.data.title,
            }
          }
        }
        let params = {
          url: '/System/UserPick/GetCustomRoleCategoryByParent',
          method: 'post',
          data:data
        };
        _this.httpService(params,(result)=>{
          if(result.data && result.status === 200) {
            if(result.data.data && result.data.data.length > 0){
              let data = result.data.data;
              if(resolve){
                resolve(data);
              }
              else{
                _this.organizationList = data;
              }
            }else{
              // 查询无数据
              if(resolve){
                resolve([]);
              }
            }
          }
          _this.$emit('closeLoadding');
        });
      },
      /**
       * 点击 tree 节点
       * @param {Object} data tree 的节点 data
       * @param {Object} node  tree 的节点 Node
       * @param {Element} e  节点组件本身
       */
      nodeClick(data,node,e){
        //清除右键菜单
        this.currentNode = node;
        this.$emit('node-click',data,node,e);
      },
      /**
       * 当前选中节点变化时触发的事件
       * @param {Object} data tree 的节点 data
       * @param {Object} node  tree 的节点 Node
       */
      currentChange(data,node){
        this.$emit('current-change',data,node);
      },
      /**
       * 节点选中状态发生变化时的回调
       * @param {Object} data  tree 的节点 data
       * @param {Boolean} self 节点本身是否被选中
       * @param {Element} self  tree 的节点本身
       */
      checkChange(data,self,child){
        this.$emit('check-change',data,self,child);
      },
      /**
       * 当复选框被点击的时候触发
       * @param {Object} data tree 的节点 data
       * @param {Object} node  tree 的节点 Node
       */
      check(data,node){
        this.$emit('check',data,node);
      },
      /**
       * 通过 key / data 设置某个节点的勾选状态
       * @param {String|Object} obj 当前要删除的数据
       * @param {Boolean} checked 当前要删除的数据
       * @param {Boolean} deep 当前要删除的数据
       */
      setChecked(obj,checked=false, deep=false){
        let _this = this;
        //多选
        if( _this.$refs.systemRoleTree){
          _this.$nextTick(()=>{
            _this.$refs.systemRoleTree.setChecked(obj,checked, deep);
          });
        }
      },
      /**
       * 通过 key 设置某个节点的当前选中状态
       * @param {String|Object} key 当前要删除的数据
       */
      setCurrentKey(key){
        let _this = this;
        _this.$nextTick(()=>{
          if(key && _this.$refs.customRoleTree) {
            _this.$refs.customRoleTree.setCurrentKey(key);
            let node = _this.$refs.customRoleTree.getNode(key);
            if (node) {
              //获取 event
              _this.$emit('node-click', node.data, node, _this.$refs.customRoleTree.$el);
            }
          }
        });
      },
      /**
       * 通过 keys 设置目前勾选的节点
       * @param {Array} keys 勾选节点的 key 的数组
       * @param {Boolean} leafOnly 若为 true 则仅设置叶子节点的选中状态
       */
      setCheckedKeys(keys, leafOnly=false){
        let _this = this;
        _this.$nextTick(()=>{
          this.$refs.customRoleTree.setCheckedKeys(keys,leafOnly);
        });
      },
      getNode(id){
        return this.$refs.systemRoleTree.getNode(id);
      },
      getCheckedNodes(){
        return  this.$refs.customRoleTree.getCheckedNodes();
      },
      /**
       * list to tree
       * @param {Array} data 数据
       * @returns Array
       */
      toTree(data) {
        if (data == null || data.length == 0) return [];
//      let list = [];
        let val = [];
        let map = {};
//      for (let item of data) {
//        let obj = Object.assign({}, item.parentId);
//        list.push(obj);
//      }
        data.forEach(function(item) {
          delete item.children;
        });
        data.forEach(function(item) {
          map[item.id] = item;
        });
        data.forEach(function(item) {
          var parent = map[item.parentId];
          if (parent) {
            (parent.children || (parent.children = [])).push(item);
          } else {
            val.push(item);
          }
        });
        return val;
      },
      //httpService 请求接口
      httpService(params,coolback){
        let _this = this;
        _this.loading = true;
        _this.$emit('openLoadding');
        _this.$axios(params).then(result => {
          _this.loading = false;
          coolback(result);
        }).catch(error => {
          _this.loading = false;
          _this.$emit('closeLoadding');
          throw error;
        });
      },

    },
    computed: {
      personTableHeight() {
        return this.organizationList.length > 0 ? 185 : 223;
      },
    }
  };
</script>




