<!--弹窗-->
<template>
  <div>
    <el-dialog :visible.sync="Visible" class="pccandiate"
               id="composite"
               @opened="openChoosen"
               @close="closeDialoging"
               :close-on-click-modal="false"
               :title="dialogTitle"
               :top="top"
               :show="Visible"
               width="700px"
               center>
      <el-tabs @tab-click="handleClick" v-model="choosenTab">
        <el-tab-pane :label=item.name :name="item.value" :key="index" v-for="(item,index) in tabName">
          <div v-if="item.value==='Collect'">
            <!--收藏组件-->
            <div class="common" v-show="isActive">
              <el-row>
                <div class="recently-content radio-content">
                  <el-row>
                    <el-col v-for="city in CollectPerson" :key="city.id" :is-checked="true" class="recently-box" :class="{'checked':city.checked==true}" :span="8">
                      <div class="picture" @click="submitAdd(city)">
                        <div class="picture-text">
                          <p :title="city.UserDisplayName" class="department">{{ city.UserDisplayName }}</p>
                          <p :title="city.UserAccount" class="staff">{{ city.UserAccount }}</p>
                        </div>
                        <template>
                          <i class="icon iconfont" :class="city.IsCollect?'icon-collect-icon-copy':'icon-noCollect-icon'"
                             @click.stop="cancelCollect(city)" style="font-size: 20px;color: #FFB800"></i>
                        </template>
                      </div>
                    </el-col>
                  </el-row>
                </div>
              </el-row>
            </div>
            <!--收藏-->
          </div>
          <!--人员组件-->
          <div v-if="item.value==='User'">
          <div v-show="isActive">
            <Choose-person-New :tags="tags"
                               :deleteTags="deleteTags"
                               :mutilselectCheck="mutilselectCheck"
                               :parmDialog="parmDialog"
                               :type="type"
                               ref="choosen"
                               @empSelectTags="empSelectTags">
            </Choose-person-New>
          </div>
        </div>
          <!--岗位-->
          <div v-if="item.value==='Org'">
            <div v-show="isActive">
              <choose-org :tags="tags"
                          :mutilselectCheck="mutilselectCheck"
                          @empSelectTags="empSelectTags"
                          :deleteTags="deleteTags">
              </choose-org>
            </div>
          </div>
          <!--组织组件-->
          <div v-if="item.value==='Position'">
            <div v-show="isActive">
              <choose-postion :tags="tags"
                              :mutilselectCheck="mutilselectCheck"
                              @empSelectTags="empSelectTags"
                              :deleteTags="deleteTags">
              </choose-postion>
            </div>
          </div>
          <!--虚拟组件-->
          <div v-if="item.value==='InventedPosition'">
            <div v-show="isActive">
              <choos-vs-position
                :tags="tags"
                :mutilselectCheck="mutilselectCheck"
                @empSelectTags="empSelectTags"
                :deleteTags="deleteTags">
              </choos-vs-position>
            </div>
          </div>
          <!--系统角色组件-->
          <div v-if="item.value==='SystemRole'">
            <div v-show="isActive">
              <choos-system-role :tags="tags"
                                 :mutilselectCheck="mutilselectCheck"
                                 @empSelectTags="empSelectTags"
                                 :deleteTags="deleteTags">
              </choos-system-role>
            </div>
          </div>
          <!--自定义角色组件-->
          <div v-if="item.value==='CustomRole'">
            <div v-show="isActive">
              <choos-custom-role :tags="tags"
                                 :mutilselectCheck="mutilselectCheck"
                                 @empSelectTags="empSelectTags"
                                 :deleteTags="deleteTags">
              </choos-custom-role>
            </div>
          </div>
          <!--流程组件-->
          <div v-if="item.value==='Process'">
            <div v-show="isActive">
              <choose-process :tags="tags"
                              :Visible="Visible"
                              :ProcessType="ProcessType"
                              :parmDialog="parmDialog"
                              :mutilselectCheck="mutilselectCheck"
                              @empSelectTags="empSelectTags"
                              :deleteTags="deleteTags">
              </choose-process>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
      <!-- 拉起效果 begin -->
      <div class="choosepersoned-top">
        <div :class="{ pullup: isActive }" class="choosepersoned">
          <div class="pull" @click="pullupClick"/>
          <div class="personnal">
            <div class="deletechoose-box">
            <span class="deletechoose" @click="clearAll">
              <i class="el-icon-delete"></i>
            </span>
            </div>
            <div :class="isActive?deletechooseheight:deletechoosecontent">
              <el-tag
                v-for="tag in tags"
                :key="tag.SysId"
                type="info"
                closable
                @close="handleClose(tag)">
                <template slot-scope="scope">
                  <div class="deleteperson">
                    <div class="picture-text">
                      <p :title="tag.UserDisplayName" class="department">{{ tag.UserDisplayName}}</p>
                      <p :title="tag.UserAccount" class="staff" v-show="tag.type==='User' || tag.Type==='User' || tag.sourcesType==='User' " >{{ tag.UserAccount }}</p>
                      <p :title="tag.displayName" class="staff" v-show="tag.typeName==='Process'|| tag.typeName==='Activity' || tag.typeName==='Version' ">{{tag.processName}}({{tag.processCode}})</p>
                      <p :title="tag.name" class="staff" v-show="tag.typeName!=='Process'">{{tag.name}}</p>
                    </div>
                  </div>
                </template>
              </el-tag>
            </div>
          </div>
        </div>
      </div>
      <!-- 拉起效果 end -->

      <!-- 弹窗底部栏 begin -->
      <div class="el-dialog__footer">
        <span slot="footer" class="dialog-footer">
          <el-button @click="cancel">取 消</el-button>
          <el-button type="primary" @click="submit">确 定</el-button>
        </span>
      </div>
      <!-- 弹窗底部栏 end -->
    </el-dialog>
  </div>
</template>
<script>
  import ChoosePersonNew from '@/components/biz/choosePersonNew.vue'
  import ChooseOrg from '@/components/biz/chooseOrg.vue'
  import ChoosePostion from '@/components/biz/choosePosition.vue'
  import choosVsPosition from '@/components/biz/chooseVsPosition.vue'
  import choosSystemRole from '@/components/biz/chooseSystemRole.vue'
  import choosCustomRole from '@/components/biz/chooseCustomRole.vue'
  import chooseProcess from '@/components/biz/chooseProcess.vue'
  import { baseUrl } from '../../../static/commonApi';
  export default {
    name: "compositeChoosen",
    components: {
      ChoosePersonNew,
      ChooseOrg,
      ChoosePostion,
      choosVsPosition,
      choosSystemRole,
      choosCustomRole,
      chooseProcess
    },
    props:{
      parmDialog: {
        type: Object,
        default: ()=>{
          return new Object()
        }
      },
      selecttype:{
        type: Boolean,
        default: false,
      },
      Visible:{
        type: Boolean,
        default: false,
      },
      portalmMtilselectCheck:{
        //portal单选多选
        type:String,
        default:''
      },
      echoData:{
        type:Array,
        default: function () { return [] }
      },
      ProcessType:{
        type: String,
        default: '',
      },
    },
    data() {
      return{
        CollectPerson:[], //收藏人员
        deletechooseheight:'deletechooseheight',
        deletechoosecontent:'deletechoose-content',
        tags:[],//确认收藏
        isActive:true,//活动面版
        choosenTab:'',
        deleteTags:{},
        tabType:'',
        type:'',
        dialogTitle:'',
        tabName:[
          {
            name:'收藏',
            value:'Collect',
            show:false
          },
          {
            name:'人员',
            value:'User',
            show:false
          },
          {
            name:'组织',
            value:'Org',
            show:false,
          },
          {
            name:'岗位',
            value:'Position',
            show:false
          },
          {
            name:'虚拟岗位',
            value:'InventedPosition',
            show:false
          },
          {
            name:'系统角色',
            value:'SystemRole',
            show:false
          },
          {
            name:'自定义角色',
            value:'CustomRole',
            show:false
          }
        ],
        tabIndex:0,
        mutilselectCheck:'',//iframe单选多选
        top:80 + 'px',
        userPickUrl:baseUrl //用户控件Url
      }
    },
    mounted() {
      var that = this;
      //是否单选和多选
      that.$nextTick(()=>{
        //portal
        if(!that.parmDialog.typeIframe){
          that.top = 80 + 'px';
          that.mutilselectCheck = that.portalmMtilselectCheck
        }
        else {
          if(that.parmDialog.formType==='form'){
            that.top = 0 + 'px';
            that.mutilselectCheck = that.portalmMtilselectCheck
          }else {
            that.top = 120 + 'px';
            that.mutilselectCheck = that.parmDialog.mutilselect;
          }
        }
      });
    },
    computed: {
      typeIframe() {
        return this.parmDialog.classifier
      },
    },
    watch:{
      //监听portal显示那个控件
      tabType:{
        handler(n, o) {
          if(n === 'User'){
            this.isActive = true;
            this.getCollect();
            this.choosenTab='User';
            this.dialogTitle = '选择人员';
            this.tabName = [{
              name:'收藏',
              value:'Collect',
              show:false
            },
              {
                name:'人员',
                value:'User',
                show:false
              }];
          }
          if(n === 'Org'){
            this.tags = this.tags.filter(function (item) {
              return item.type === 'Org'
            });
            this.isActive = true;
            this.dialogTitle = '选择组织';
            this.choosenTab='Org';
            this.tabName = [{
              name:'组织',
              value:'Org',
              show:false,
            }] ;
          }
          if(n === 'Position'){
            this.tags = this.tags.filter(function (item) {
              return item.type==='Position'
            });
            this.dialogTitle = '选择岗位';
            this.choosenTab='Position';
            this.tabName = [{
              name:'岗位',
              value:'Position',
              show:false,
            }] ;

          }
          if(n === 'InventedPosition'){
            this.tags = this.tags.filter(function (item) {
              return item.type==='InventedPosition'
            });
            this.dialogTitle = '选择虚拟岗位';
            this.choosenTab='InventedPosition';
            this.tabName = [{
              name:'虚拟岗位',
              value:'vposition',
              show:false,
            }] ;
          }
          if(n === 'SystemRole'){
            this.tags = this.tags.filter(function (item) {
              return item.type==='InventedPosition'
            });
            this.dialogTitle = 'SystemRole';
            this.choosenTab='SystemRole';
            this.tabName = [{
              name:'系统角色',
              value:'systemrole',
              show:false,
            }] ;
          }
          if(n === 'CustomRole'){
            this.tags = this.tags.filter(function (item) {
              return item.type==='InventedPosition'
            });
            this.dialogTitle = 'CustomRole';
            this.choosenTab='CustomRole';
            this.tabName = [{
              name:'自定义角色',
              value:'customrole',
              show:false,
            }] ;
          }
          if(n === 'Process'){
            this.tags = this.tags.filter(function (item) {
              return item.type==='Process'
            });
            this.isActive = true;
            this.dialogTitle = '流程';
            this.choosenTab='Process';
            this.tabName = [{
              name:'流程',
              value:'Process',
              show:false,
            }] ;
          }
          if(n==='all'){
            this.dialogTitle = '选人控件';
            this.tabName = [{
              name:'收藏',
              value:'User',
              show:false
            },
              {
                name:'人员',
                value:'User',
                show:false
              },{
                name:'组织',
                value:'org',
                show:false,
              },
              {
                name:'岗位',
                value:'position',
                show:false,
              },
              {
                name:'虚拟岗位',
                value:'vposition',
                show:false,
              },
              {
                name:'系统角色',
                value:'systemrole',
                show:false,
              },
              {
                name:'自定义角色',
                value:'customrole',
                show:false,
              }
            ];
          }
        },
        deep: true,
        immediate: true
      },
      //监听iframe显示那个控件
      typeIframe:{
        handler(n, o) {
          let _this = this;
          //选人控件
          if(n===undefined || n===''){
            return
          }
          if(n==='UserPick'){
            //this.getCollect();
            if(this.parmDialog.typeIframe==='All'){
              this.choosenTab = 'Collect';
              this.dialogTitle = '我的收藏';
              this.tabName = [
                {
                  name:'收藏',
                  value:'Collect',
                  show:false
                },
                {
                  name:'人员',
                  value:'User',
                  show:false
                },
                {
                  name:'组织',
                  value:'Org',
                  show:false,
                },
                {
                  name:'岗位',
                  value:'Position',
                  show:false,
                },
                {
                  name:'虚拟岗位',
                  value:'InventedPosition',
                  show:false,
                },
                {
                  name:'系统角色',
                  value:'SystemRole',
                  show:false,
                },
                {
                  name:'自定义角色',
                  value:'CustomRole',
                  show:false,
                }
              ];
            }
            else {
              let UserArray = [];
              let temp =Object.assign([],this.tabName);
              UserArray = this.parmDialog.typeIframe.split(',');
              if(this.parmDialog.typeIframe.indexOf('User')>-1){
                this.getCollect();
                UserArray.unshift('Collect')
              }
              this.tabName = temp.filter(function (item) {
                return UserArray.includes(item.value)
              });
              _this.isActive = true;
              this.$set(this,'choosenTab',_this.tabName[0].value);
              this.$set(this,'dialogTitle','选择'+_this.tabName[0].name);
            }
          }
          //选流程
          else {
            this.isActive = true;
            this.choosenTab='Process';
            this.dialogTitle = '流程';
            this.tabName = [{
              name:'流程',
              value:'Process',
              show:false,
            }] ;
          }
        },
        immediate: true
      },
      //监听弹窗状态
      Visible:{
        handler(n,o){
          if(n){
            let that = this;
            //portal
            if(!that.parmDialog.typeIframe){
              that.portalType(that.tabType);
            }
          }else {
            this.mutilselectCheck = 'false';
          }
        },
        immediate: true
      },
      //回显数据
      echoData:{
        handler(n,o){
          this.tags=[];
          let obj = {};
          if(this.parmDialog.typeIframe) {
            for (let item of n) {
              let obj = {};
              //iframe模式
              obj.UserDisplayName = item.Name;
              obj.UserAccount = item.Value;
              obj.type = item.Type;

              if (item.Type !== 'User') {
                obj.SysId = item.Value;
              }
            }
            this.$nextTick(()=>{
              this.tags.push(obj);
            })
          }
          else{
            //portal模式
            for (let item of n) {
              if(item.portalType){
                if (item.type === 'Process') {
                  obj.typeName = item.type;
                  obj.processName = item.ProcessName;
                  obj.processCode = item.ProcessCode;
                  obj.type = item.type;
                  obj.id = item.id;
                }
                else if (item.type === 'Category') {
                  obj.name = item.categoryName;
                  obj.categoryName = item.categoryName;
                  obj.typeName = item.type;
                  obj.type = item.type;
                  obj.id = item.id;
                }
                else {
                  obj.SysId = item.SysId;
                  obj.UserDisplayName = item.UserDisplayName;
                  obj.UserAccount = item.UserAccount;
                  obj.type = item.type;
                }
              }
              else {
                if (item.type === 'Process') {
                  obj.typeName = item.type;
                  obj.processName = item.ProcessName;
                  obj.processCode = item.ProcessCode;
                  obj.type = item.type;
                  obj.id = item.id;
                }
                else if (item.type === 'Category') {
                  obj.name = item.categoryName;
                  obj.categoryName = item.categoryName;
                  obj.typeName = item.type;
                  obj.type = item.type;
                  obj.id = item.id;
                }
                else {
                  obj.SysId = item.SysId;
                  obj.UserDisplayName = item.UserDisplayName;
                  obj.UserAccount = item.UserAccount;
                  obj.type = item.type;
                }
              }
              this.$nextTick(()=>{
                this.tags.push(obj);
              })
            }
          }
        },
        immediate: true
      },
    },
    methods: {
      //切换显示组件
      handleClick:function (tab, event) {
        var that = this;
        that.isActive = true;
        switch (tab.name){
          case 'User':
            that.type='User';
            that.dialogTitle = '选择人员';
            that.getCollect();
            break;
          case 'Collect':
            that.type='Collect';
            that.dialogTitle = '选择人员';
            that.getCollect();
            break;
          case 'Org':
            that.dialogTitle = '选择组织';
            that.type='Org';
            break;
          case 'Position':
            that.dialogTitle = '选择岗位';
            that.type='Position';
            break;
          case 'InventedPosition':
            that.dialogTitle = '选择虚拟岗位';
            that.type='InventedPosition';
            break;
          case 'SystemRole':
            that.dialogTitle = '选择系统角色';
            that.type='SystemRole';
            break;
          case 'CustomRole':
            that.dialogTitle = '选择自定义角色';
            that.type='CustomRole';
            break;
          case 'Process':
            that.dialogTitle = '选择流程';
            that.type = 'Process';
            break;
        }
      },
      // 获取收藏接口
      getCollect:function () {
        var that = this;
        that.$axios({
          url: that.userPickUrl.Base_User_URL + '/GetUserListByCollect',
          method:'POST',
        }).then(res=>{
          if(res.status===200){
            that.CollectPerson = res.data.data;
          }
        }).catch(err=>{
          that.$message.error(`数据获取失败!ERROR MESSAGE:${err.message}`);
        })
      },
      //取消收藏
      cancelCollect:function (val) {
        var that = this;
        var data={
          IsCollect:false,
          refUserId:val.SysId
        };
        that.$axios({
          url:that.userPickUrl.Base_User_URL + '/SetCollectPerson',
          method:'POST',
          data:data
        }).then(res=>{
          if(res.data.code===0){
            that.CollectPerson.splice(that.CollectPerson.indexOf(val), 1);
            val.IsCollect = !val.IsCollect
          }else {
            that.$message.error('数据获取失败');
          }
        }).catch(err=>{
          that.$message.error(`数据获取失败!ERROR MESSAGE:${err.message}`);
        })
      },
      //点击数据拉到下面tags
      submitAdd(val){
        var that = this;
        //多选
        if(that.mutilselectCheck==='true'){
          val.checked = true
          if(that.tags && that.tags.length > 0){
            var Account = that.getPersonAccount(that.tags);
            if(!Account.includes(val.UserAccount)){
              that.tags.push(val)
            }
          } else {
            that.tags.push(val)
          }
        }
        //单选
        else {
          for (let item of that.CollectPerson){
            item.checked = false
          }
          val.checked = true;
          if(that.tags && that.tags.length >= 1){
            that.tags = that.tags.splice(that.tags.length,0);
            that.tags.push(val)
          }
          else {
            that.tags.push(val)
          }
        }
        for(let item of that.tags){
          item.type = 'User'
        }
      },
      //打开弹窗
      openChoosen(val,type){
        let that = this;
        //用于portal控制控件显示
        if(type){
          that.tabType = type;
        }else {
          that.tabType = val;
        }
        that.$emit('OpenDialog',true);
      },
      //关闭弹窗
      cancel(){
        let that = this;
        that.$emit('CloseDialog',false)
      },
      //点击×
      closeDialoging(){
        let that = this;
        if(that.parmDialog.typeIframe) {
          that.$emit('handleCloseDialog',false);
        }
        else {
          that.$emit('CloseDialog',false)
        }
      },
      //pullup上拉
      pullupClick(){
        this.isActive = !this.isActive;
      },
      //清空
      clearAll(){
        var that = this;
        that.tags = [];
      },
      //单个删除
      handleClose(tag){
        var that = this;
        that.deleteTags = tag;
        var index = that.tags.indexOf(tag);
        that.tags.splice(index,1);
      },
      //获取唯一id
      getPersonId(list){
        var onlySid = [];
        for(let v of list){
          let ssid = v.SysId;
          if(ssid){
            onlySid.push(ssid)
          }
        }
        return onlySid
      },
      //确定按钮
      submit(){
        var that = this;
        that.$emit('OpenDialog',false);
        //传值给ifarme父组件开始
        that.$emit('sureSubmit',that.tags);
        //传值给portal父组件结束
        that.sendTagSelect(that.tags, that.tabType);
      },
      //最终获得的tags
      empSelectTags(val) {
        let that = this;
        that.tags = val;
        //数组去重
        that.tags=Array.from(new Set(that.tags));
      },
      //发送tags用于父组件
      sendTagSelect(val,type){
        if(this.ProcessType){
          for (let item of val){
            item.portalType = this.ProcessType
          }
        }
        this.$emit('sendTag',val,type)
      },
      //用于接收portal回显
      portalehco(val){
        this.tags = val;
      },
      //获取用户名
      getPersonAccount(list){
        var onlySid = [];
        for(let item of list){
          let ssid = item.UserAccount;
          if(ssid){
            onlySid.push(ssid)
          }
        }
        return onlySid
      },
      //初始加载弹窗
      portalType(n){
        if(n === 'User'){
          this.isActive = true;
          this.getCollect();
          this.choosenTab='User';
          this.dialogTitle = '选择人员';
          this.tabName = [
            {
              name:'收藏',
              value:'Collect',
              show:false
            },
            {
              name:'人员',
              value:'User',
              show:false
            }
          ];
        }  //人员
        if(n === 'CurrentUser'){
          this.isActive = true;
          this.getCollect();
          this.choosenTab='Collect';
          this.dialogTitle = '选择人员';
          this.tabName = [
            {
              name:'收藏',
              value:'Collect',
              show:false
            },
            {
              name:'人员',
              value:'User',
              show:false
            }
          ];
        } //当前处理人
        if(n === 'ToWrite'){
          this.isActive = true;
          this.getCollect();
          this.choosenTab='Collect';
          this.dialogTitle = '选择人员';
          this.tabName = [
            {
              name:'收藏',
              value:'Collect',
              show:false
            },
            {
              name:'人员',
              value:'User',
              show:false
            }
          ];
        }  //抄送人
        if(n === 'Submitter'){
          this.isActive = true;
          this.getCollect();
          this.choosenTab='Collect';
          this.dialogTitle = '选择人员';
          this.tabName = [
            {
              name:'收藏',
              value:'Collect',
              show:false
            },
            {
              name:'人员',
              value:'User',
              show:false
            }
          ];
        } //提交人
        if(n === 'Org'){
          this.isActive = true;
          this.dialogTitle = '选择组织';
          this.choosenTab='Org';
          this.tabName = [{
            name:'组织',
            value:'Org',
            show:false,
          }] ;
        }
        if(n === 'Position'){
          this.dialogTitle = '选择岗位';
          this.choosenTab='Position';
          this.tabName = [{
            name:'岗位',
            value:'Position',
            show:false,
          }] ;

        }
        if(n === 'InventedPosition'){
          this.dialogTitle = '选择虚拟岗位';
          this.choosenTab='InventedPosition';
          this.tabName = [{
            name:'虚拟岗位',
            value:'vposition',
            show:false,
          }] ;
        }
        if(n === 'SystemRole'){
          this.dialogTitle = 'SystemRole';
          this.choosenTab='SystemRole';
          this.tabName = [{
            name:'系统角色',
            value:'systemrole',
            show:false,
          }] ;
        }
        if(n === 'CustomRole'){
          this.dialogTitle = 'CustomRole';
          this.choosenTab='CustomRole';
          this.tabName = [{
            name:'自定义角色',
            value:'customrole',
            show:false,
          }] ;
        }
        if(n === 'Process'){
          this.isActive = true;
          this.dialogTitle = '流程';
          this.choosenTab='Process';
          this.tabName = [{
            name:'流程',
            value:'Process',
            show:false,
          }];
          this.mutilselectCheck='true';
        }
        if(n === 'Category'){
          this.isActive = true;
          this.dialogTitle = '流程';
          this.choosenTab='Process';
          this.tabName = [{
            name:'流程',
            value:'Process',
            show:false,
          }] ;
          this.mutilselectCheck='true';
        }
        if(n==='userSelectFell'){
          this.isActive = true;
          this.getCollect();
          this.choosenTab='Collect';
          this.mutilselectCheck = 'true';
          this.dialogTitle = '选择人员';
          this.tabName = [
            {
              name:'收藏',
              value:'Collect',
              show:false
            },
            {
              name:'人员',
              value:'User',
              show:false
            }];
        }  //
        if(n==='all'){
          this.dialogTitle = '选人控件';
          this.tabName = [{
            name:'收藏',
            value:'User',
            show:false
          },
            {
              name:'人员',
              value:'User',
              show:false
            },{
              name:'组织',
              value:'org',
              show:false,
            },
            {
              name:'岗位',
              value:'position',
              show:false,
            },
            {
              name:'虚拟岗位',
              value:'vposition',
              show:false,
            },
            {
              name:'系统角色',
              value:'systemrole',
              show:false,
            },
            {
              name:'自定义角色',
              value:'customrole',
              show:false,
            }
          ];
        }
        if(n===''){
          this.tabName = [];
        }
      },
    },
  }
</script>
<style>
  .picture{
    display: flex;
    justify-content: space-between;
  }

  .picture-text{
    margin: 5px 15px 10px 0;
  }
  .deleteperson .picture-text{
    margin: 5px 15px 10px 0;
    padding-top: 5px;
  }
  .department{
    margin: 8px 0;
  }
  .picture i{
    display: flex;
    align-items: center;
    margin-right: 15px;
  }
  .pull{
    width: 47px;
    height: 9px;
    background: url(../../assets/pullup.png) no-repeat 0 0;
    margin: 0 auto;
    background-size: cover;
    cursor: pointer;
  }
  .deletechoose-box{
    width: 100%;
    line-height: 40px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: end;
    -ms-flex-pack: end;
    justify-content: flex-end;
  }
  .deletechoose-box .el-icon-delete{
    font-size: 13px;
    color: #2f91df;
    cursor: pointer;
  }
  .el-icon-delete:before{
    padding-right: 10px;
  }
  .choosepersoned-top .choosepersoned .personnal .deletechoose-content{
    /*height: 497px;*/
    overflow-x: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: start;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    padding-bottom: 270px;
    height: 225px;
  }
  .choosepersoned-top .choosepersoned .personnal .deletechooseheight {
    padding: 8px 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    overflow: auto;
    height: 80px;
    max-height: 150px;
  }
  .el-dialog__footer{
    padding: 10px;
    text-align: center;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    background-color: #f5f7fa;
    border-bottom-left-radius: 2px;
    border-bottom-right-radius: 2px;
  }
  .choosepersoned-top .choosepersoned .personnal .el-tag{
    position: relative;
    height: 60px;
    width: 154px;
    line-height: 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    float: left;
    margin-bottom: 10px;
    overflow: hidden;
    margin-right: 10px;
    background-color: #eaf4fc;
    border: none;
    border-radius: 2px;
  }
  .el-tag--info {
    position: relative;
    height: 60px;
    width: 180px;
    line-height: 10px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    float: left;
    margin-bottom: 10px;
    overflow: hidden;
    margin-right: 16px;
    background-color: #eaf4fc;
    border: none;
    border-radius: 2px;
  }
  .choosepersoned-top .choosepersoned .personnal .el-tag .deleteperson {
    height: 50px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }
  .choosepersoned-top .choosepersoned .personnal .el-tag .el-icon-close {
    position: absolute;
    right: 5px;
    top: 3px;
    font-size: 12px;
    color: #2f91df;
    font-weight: bolder;
  }
  .choosepersoned-top .choosepersoned .personnal .el-tag .department, .choosepersoned-top .choosepersoned .personnal .el-tag .staff {
    width: 135px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin: 0;
    color: #212d3d;
    line-height: 19px
  }
  .collectIcon{
    display: block;
    position: relative;
    z-index: 10;
  }
.recently-box{
  height: 60px;

  margin: 2px 0 0 0;
  box-sizing: border-box;
 }
 .recently-box.checked{
   background:#eaf4fc;
 }
.recently-box .picture{
  padding-left: 8px;
}
</style>



