<template>
  <div class="originater_dialog">
    <!--<div class="mask"></div>-->
    <div :class="[closeShow?'pc-candidates':'pc-candidates pc-background']">
      <div class="dialog_content">
          <div class="top" v-show="!closeShow">流程选择
            <span style="font-size: 30px;cursor:pointer;float:right;padding-right:5px" @click="close">×</span>
            </div>
          <div class="initProcess">
            <el-container>
              <el-header>
                <div class="initProcess-header">
                  <!-- <span class="initProcess-breadCrumb">发起流程</span> -->

                  <el-checkbox :disabled="isK2ProcessDisabled" @change="checkIsK2Process" class="initCheckbox" v-model="checked">显示允许直接从K2发起的流程</el-checkbox>
                  <div class="searchCom">
                    <el-input :disabled="isK2ProcessDisabled" @keyup.enter.native="searchFlow" clearable class="inputCom" v-model="processValue" placeholder="流程搜索" size="mini"></el-input>
                    <el-button :disabled="isK2ProcessDisabled" class="searchBtn" type="primary" icon="el-icon-search" size="mini" @click="searchFlow">搜索</el-button>
                  </div> 

                </div>
              </el-header>
              <el-main>
                <!-- width: 100%;  -->
                <div style="overflow-x: hidden;">
                  <div class="originater-contain">
                    <!-- 一级列表 -->
                    <ul class="secondList">
                      <li v-for="secItem in secondData" :class="{isActive: isSecondSelcet === secItem.orgName}" :key="secItem.orgName" @click="checkSecond(secItem)">
                        <i class="el-icon-tickets"></i>
                        {{secItem.orgName}}
                        </li>
                    </ul>
                      <!-- 二级列表 -->
                    <ul class="thirdList">
                      <li v-for="thiItem in thirdData" :class="{isActive: isThirdSelcet === thiItem.id}" :key="thiItem.id" @click.stop="checkThird(thiItem)">{{thiItem.name}}</li>
                    </ul>
                       <!-- 三级列表 -->
                      <ul class="fouthList">
                        <li class="fouthList-title" v-for="item in fouthData" :key="item.id"  @click.stop="checkFouth(item)">
                           <div v-if="item.processFullName==null">
                              <!-- 文件夹 -->
                              <span class="fouthList-title-first">{{item.name}}></span>
                              <div class="line"></div>
                          </div>
                          <div v-else>
                            <!-- 最后的流程 -->
                            <ul class="fifthList-contain">
                                <li class="fifthList" :class="{isActive: isSixSelcet === item.id}"  @click.stop="checkSixth(item)">
                                    <span :title="item.name">
                                    <!--修改的跳转链接部分-->
                                      <a v-if="item.disabled == false" target="_blank" :href="linkhref">{{ item.name }}</a>
                                      <a v-else>{{ item.name }}</a>
                                    </span>
                                </li>
                              </ul>
                          </div>
                          <!-- 四级列表 -->
                          <ul>
                            <li class="fouthListCon" v-for="child in item.Child" :key="child.id" :class="[{fouthChild:child.Child},{isActive: isFifthSelcet === child.id}]" @click.stop="checkFifth(child)">
                                    <span :title="child.name">
                                    <!--修改的跳转链接部分-->
                                      <a v-if="child.disabled == false" target="_blank" :href="linkhref">{{ child.name }}</a>
                                      <a v-else>{{ child.name }}</a>
                                    </span>
                              <ul class="fifthList-contain">
                                <li class="fifthList" :class="{isActive: isSixSelcet === children.id}" v-for="children in child.Child" :key="children.id" @click.stop="checkSixth(children)">
                                    <span :title="children.name">
                                    <!--修改的跳转链接部分-->
                                      <a v-if="children.disabled == false" target="_blank" :href="linkhref">{{ children.name }}</a>
                                      <a v-else>{{ children.name }}</a>
                                    </span>
                                </li>
                              </ul>
                            </li>
                          </ul>
                        </li>
                      </ul>
                  </div>
                  <div class="addrecently-footer">
                    <el-button type="primary" icon="el-icon-circle-check-outline" size="mini" @click="sureSubmitData">确定</el-button>
                    <el-button type="primary" icon="el-icon-circle-close-outline" size="mini" @click="close">取消</el-button>
                  </div>
                </div>
                  
              </el-main>
              </el-container>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "processDialog",
  props:{
    closeShow: false
  },
  data() {
    return {
      dialog: true,
      orgID: "",
      // todoSearch: '',
      // placeholder: '流程搜索:',
      // inputSearch: '',
      isFirstSelcet: "",
      isSecondSelcet: "",
      isThirdSelcet: "",
      isFouthSelcet: "",
      isFifthSelect: "",
      isFifthSelcet: "",
      isSixSelcet: "",
      // 传给父组件的四级数据
      selectFouthData: "",
      checked: false,
      processValue: "",
      defaultProps: {
        children: "m_ChildOrg",
        label: "orgName"
      },
      // 数据
      firstData: [],
      secondData: [],
      thirdData: [],
      fouthData: [],
      // 选中数据
      selectedData: {},
      isK2ProcessDisabled: true, // 是否禁用 从k2发起的流程复选框
      loading: false
    };
  },
  mounted() {
    this.getDataList();
  },
  methods: {
    getDataList() {
      this.$axios.post("/api/companytree/page", {}).then(result => {
          if (result.status === 200) {
            this.firstData = result.data.companyTreeList;
            this.secondData = this.firstData[0].m_ChildOrg;
          } else {
            this.$message.error("请求失败！");
          }
        })
        .catch(err => {
          this.$message.error("连接服务器失败！" + err);
        });
    },

    /**
     * 点击部门  一级节点
     */
    checkSecond(data, index) {
      this.isK2ProcessDisabled = false;
      this.isSecondSelcet = data.orgName;
        this.thirdData = [];
        this.fouthData = [];
        this.isThirdSelcet = "";
        this.isFouthSelcet = "";
        this.isFifthSelect = "";
      this.orgID = data.orgID;
      var obj = {
        orgId: data.orgID,
        isUse: 1,
        name: "",
        isHide: this.checked ? 0 : 1,
        isDailog: 1
      };
      this.getUrlList(obj)
    },
    // 请求二接口数据
    getUrlList(obj) {
      this.thirdData = [];
      this.fouthData = [];
      this.isFifthSelcet = '';
      this.isSixSelcet = '';
      var url = "/api/processCategoryTree";
      this.$axios.post(url, obj).then(res => {
          if (res.status === 200) {
            this.thirdData = this.toTree(this.processingData(res.data.result));
            if (this.thirdData == null) {
              this.$message.success("数据为空");
            }
            this.checkThird(this.thirdData[0], 0);
          } else {
            this.$message.error("请求失败！");
          }
        })
        .catch(err => {
          // this.$message.error(`连接服务器失败！${err}`);
        });
    },
    toTree(data) {
       if(data==null || data.length==0) return [];
      data.forEach(function (item) {
        delete item.Child;
      });

      var map = {};
      data.forEach(function (item) {
        map[item.id] = item;
      });

      var val = [];
      data.forEach(function (item) {
        var parent = map[item.pid];
        if (parent) {
          (parent.Child || ( parent.Child = [] )).push(item);
        } else {
          val.push(item);
        }
      });
      return val;
    },
  /**
   * 点击  二级节点
   */
   checkThird(data) {
      this.isThirdSelcet = data.id;
      if (this.fouthData) {
        this.fouthData = [];
        this.isFouthSelcet = "";
        this.isFifthSelect = "";
      }
      if (data.Child) {
        this.fouthData = data.Child;
      }else{
         this.selectedData = data;
      }
    },
    /**
     * 处理显示标题
     */
    processingData(param) {
      let data=param;
      for (let item in data) {
        let childData = data[item];
        data[item].processDescriptions=[{'txt':'无提示信息!'}];
        if(childData.processDescription && childData.processDescription!=null){
          let processArray = [];
          processArray = childData.processDescription.split(";");
          let processArrayObj = [];
          for (let pro of processArray) {
            pro = pro.replace("&gt", ">");
            processArrayObj.push({ txt: pro });
          }
          data[item].processDescriptions=processArrayObj;
        }
      }
      return data;
    },
  /**
   * 点击  三级节点
   */
    checkFouth(data) {
      if (data.childsProcessCategories) {
        return;
      } else {
        this.selectedData = data;
      }
    },
    checkFifth(data) {
      if (data.childsProcessCategories) {
        return;
      } else {
        this.isSixSelcet = '';
        this.isFifthSelcet = data.id;
        this.selectedData = data;
      }
    },
    checkSixth(data) {
      this.isFifthSelcet = '';
      this.isSixSelcet = data.id;
      if (data.childsProcessCategories) {
        return;
      } else {
        this.selectedData = data;
      }
    },
    // 确认
    sureSubmitData() {
      var subData =  this.selectedData.name;
      var idData = this.selectedData.id+''; //在动态报表中的流程选择需要获取 id
      if (!subData) {
        return;
      }
      this.$emit("sureSubmit", {
        type: "process",
        text: subData,
        processID: idData,
        item: this.selectedData
      });
    },
    // 切换选择框
    checkIsK2Process () {
      var obj = {
        orgId: this.orgID,
        isUse: 1,
        isHide: this.checked ? 0 : 1,
        name: this.processValue,
        isDailog: 1
      };
      this.getUrlList(obj)
    },
    // 搜索
    searchFlow() {
      var obj = {
        orgId: this.orgID,
        isUse: 1,
        isHide: this.checked ? 0 : 1,
        name: this.processValue,
        isDailog: 1
      };
      this.getUrlList(obj)
    },
    //所有包涵输入的文本 数据
    searchScreenData(name,data){
        let newData = [];
        let child_data = []; //保存所有搜索的数据节点
        data.forEach(function(item, index, array){
          item.name.indexOf(name) !== -1?child_data.push(item):null;
        });
        if(child_data==[]) return null;
        for(let item of child_data){
          this.selectPrantData(item,data,newData);
        }
        return newData;
    },
    //查找所有父级节点
    selectPrantData(param,data,newData){
      for(let it of data){
        if(it.id==param.pid){
          //判断是否已经添加过子节点
          if(it.Child == undefined){
              it.Child =[];
          }
          it.Child.push(param);
          let isExist = true;
          for(let item of newData){
            it.id == item.id?isExist = false:isExist = true;
          }
          if(isExist){
             newData.push(it);
          }
          this.selectPrantData(it,data,newData);
        }
      }
    },
    close() {
      this.$emit("hideComponent");
    }
  }
};
</script>

<style scoped lang="scss">
.originater_dialog {

  // 列表选中，字体变色
  .fouthListCon.isActive>span>a,
  .secondList .isActive,
  .thirdList .isActive{
    color: #ffffff;
  }

  .pc-candidates {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 999;

    .dialog_content {
      position: absolute;
      top: 54%;
      left: 50%;
      transform: translate(-50%, -50%);
      border-radius: 15px;
      width: 1123px;
      .top {
        display: flex;
        justify-content: space-between;
        font-size: 16px;
        color: #fff;
        height: 50px;
        line-height: 50px;
        background: #38acff;
        background: -o-linear-gradient(left, #3dd7fe , #3195f8); /* Opera 11.1 - 12.0 */
        background: -moz-linear-gradient(left, #3dd7fe , #3195f8); /* Firefox 3.6 - 15 */
        background: linear-gradient(to left,  #3dd7fe , #3195f8); /* 标准的语法 */
        padding: 0 20px;
        padding: 0 20px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
      }
    }
  }
  .initProcess {

    background-color: #ffffff;
    border: 1px solid #38acff;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;

      .el-header {
        padding: 10px 10px 0 0;
        border-top-right-radius: 10px;
        text-align: left;
        .initProcess-header {
          height: 50px;
          line-height: 50px;
          background: #ffffff;
          border-radius: 5px;
          padding-left: 10px;
          position: relative;
          .initProcess-breadCrumb {
            // background: purple;
            font-size: 16px;
          }
          .initCheckbox {
            position: absolute;
            right: 420px;
          }
          .searchCom {
            width: 400px;
            height: 40px;
            line-height: 40px;
            position: absolute;
            right: 0;
            top: 5px;
            // background: red;
            .inputCom {
              position: absolute;
              right: 105px;
              width: 300px;
              // height: 30px;
              // line-height: 30px;
            }
            .searchBtn {
              position: absolute;
              right: 10px;
              top:6px;
            }
          }
        }
      }
      .el-main {
        padding: 0;
        position: relative;
        height: 386px;
        .originater-contain {
          width: 350px;
          height: 290px;
          margin-top: 2px;
          border-top: 2px solid #eee;
          border-bottom: 1px solid #eee;
          .firstList,.secondList,.thirdList {
          list-style: none;
          position: absolute;
          width: 180px;
          height: 100%;
          margin: 0;
          // font-size: 14px;
           font-size: 13px;
          text-align: left;
          height: 100%;
          border-left: 2px solid #eee;
          & > li {
            margin-bottom: 1px;
            cursor: pointer;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
            height: 40px;
            line-height: 40px;
            background: #ffffff;
            padding: 0 10px;
            border-bottom: 1px solid #eee;
          }
          .isActive {
            background: #1C95FE;
          }
        }
        .secondList {
          height: 300px;
          overflow-y: auto;
          margin-left: 0px;
        }
        .thirdList {
          margin-left: 180px;
          height: 300px;
          overflow-y: auto;
          & > li {
            height: 30px;
            line-height: 30px;
          }
        }
        .fouthList {
          margin-left:360px;
          height: 300px;
          // width: 840px;
          width: 730px;
          padding: 0 10px;
          overflow-y: auto;
          .fouthList-title {
            text-align: left;
            .fouthList-title-first {
              // color: #1C95FE;
              //  background: red;
              width: 100%;
              //  border-bottom: 3px solid #dddddd;
            }
            .line {
              margin-top: 5px;
              width: 100%;
              height: 2px;
              background-color: #F4F7F9;
              margin-bottom: 10px;
            }
             .fouthListCon {
                font-size: 12px;
                color: #000000;
                // background: green;
                background: #F4F7F9;
                margin-bottom: 2px;
                list-style: none;
                display: inline-block;
                padding: 6px 25px;
                margin: 15px;
                width: 160px;
                vertical-align: top;
                cursor: pointer;

                 margin-top: 0px;
                 margin-bottom: 10px;

                &.isActive {
                  background: #1C95FE;
                  color: #ffffff;
                  .fouthList-contain {
                    background: #1C95FE;
                    & i {
                      margin-right: 5px;
                    }
                  }
                  }
                &.fouthChild {
                  background: #ffffff;
                  width: 130px;
                  text-align: right;
                  font-weight: bolder;
                  display: block;
                }
                .fouthList-contain {
                    background: #F4F7F9;
                    display: inline-block;
                    & i {
                      margin-right: 5px;
                    }
                  }
              }

            .fifthList-contain {
              position: relative;
              // background-color: red;
              width: 630px;
              top: 0px; //修改三级标题 定位
              left: 10px;
              text-align: left;
            }
          }
        }

        .fifthList {
          font-weight: normal;
          background: #F4F7F9;
          margin-bottom: 2px;
          margin-left: 0;
          list-style: none;
          display: inline-block;
          padding: 6px 25px;
          margin: 5px;
          width: 160px;
          vertical-align: top;
          &.isActive {
            background: #1C95FE;
            a{
              color: #ffffff;
            }
            }
          }
        }
      }
    }
    .addrecently-footer{
      // height: 46px;
      // padding:10px;
      // text-align: center;
      // position: absolute;
      // bottom: 0px;
      // left: 42%;
      border-bottom: 0px solid #c0c0c0!important;
        a{
          color: #1fa2ff;
          position: absolute;
          top:16px;
          left:20px;
          text-decoration: none;

          i{
            font-weight:100;
            font-size:12px;
            padding-right: 5px;
          }

          span{
            text-decoration: underline;
          }
      }
    }
  }

   // tooltip弹出框
  .el-tooltip__popper.is-light.tooltip-table{
    width: 560px;
    padding: 20px;
    border:1px solid #0089ff;

    table{
      width:100%;
      text-align: left;
      color:#626262;
      border-collapse:separate;
      border-spacing:0;
      border-bottom: 1px solid #e1e1e1;
      border-right: 1px solid #e1e1e1;
      border-left: 1px solid #e1e1e1;

      .flowchart{
        width: 100%;

        .name{
          text-align: center;
          padding:10px;
          width: 120px;
          height: 100%;
          border-top: 1px solid #e1e1e1;
          border-right: 1px solid #e1e1e1;
        }
        .des{
          width: 120px;
          padding:10px;
          border-top: 1px solid #e1e1e1;
          line-height: 20px;
          text-align: center;
        }
      }
    }

    & .flowchartImg{
      padding:20px 10px;

      h4{
        color:#626262;
        text-align: left;
        margin-bottom: 20px;
      }
    }
  }
</style>



