<!--左侧树结构-->
<template>
  <div>
    <!-- 满行搜索条 begin -->
    <div class="search-condition btn-linkage" style="margin-bottom: 15px">
      <el-row>
        <el-col :span="20" class="serchCOL">
          <el-input v-model="searchTxt"
                    placeholder="流程名称"
                    clearable
                    @clear="clearText"
                    @keyup.enter.native="handleSearch"
                    class="searchText">
          </el-input>
        </el-col>
        <el-col :span="4">
          <div class="searchline" style="text-align: right">
            <el-button type="primary"
                       icon="el-icon-search"
                       @click="handleSearch"
                       class="choosenBtn">搜索
            </el-button>
          </div>
        </el-col>
      </el-row>
    </div>
    <!-- 满行搜索条 end -->
    <div class="dialogScroll">
      <el-tree
        ref="systemRoleTree"
        :node-key="nodKey"
        :props="defaultProps"
        show-checkbox
        :default-checked-keys="defaultCheckedKeys"
        :data="processlist"
        :expand-on-click-node="false"
        :check-strictly="checkStrictly"
        :highlight-current="highlightCurrent"
        :check-on-click-node="false"
        :auto-expand-parent="autoExpandParent"
        :load="loadNodeData"
        :loading="loading"
        :height="personTableHeight"
        lazy
        @node-click="nodeClick"
        @current-change="currentChange"
        @check-change="checkChange"
        @check="check">
      </el-tree>
    </div>
  </div>
</template>
<script>
  // `debounce` 是一个通过 Lodash 限制操作频率的函数。
  import {debounce} from 'lodash';
  import { baseUrl } from '../../../static/commonApi';

  export default {
    name: 'processTree',
    props: {
      // tree 主键 key
      nodKey:{
        type:String,
        default:'id',
        require:false
      },
      //是否高亮显示
      highlightCurrent:{
        require: false,
        type: Boolean,
        default: true
      },
      //是否在点击节点的时候展开或者收缩节点
      expandOnClickNode:{
        type:Boolean,
        require:false,
        default:true
      },
      //是否在第一次展开某个树节点后才渲染其子节点
      renderexpand:{
        type:Boolean,
        require:false,
        default:true,
      },
      //展开子节点的时候是否自动展开父节点
      autoExpandParent:{
        type:Boolean,
        require:false,
        default:true,
      },

      // 懒加载是否用 IdPath做查询条件
      depthIdPath:{
        type:Boolean,
        require:false,
        default:false,
      },
      // 是否在页面加载时主动获取数据
      isMountedGetData:{
        type:Boolean,
        require:false,
        default:true,
      },
      // 默认根节点数据
      defaultRootNode:{
        type:Object,
        require:false,
        default:null,
      },
      // 默认显示名称字段 扩展显示名称字段
      defaultPropLabel:{
        type:String,
        require:false,
        default:'displayName',
      },
      // 默认排序字段 扩展排序字段
      defaultSort:{
        type:String,
        require:false,
        default:'orderIndex',
      },
      mutilselectCheck:{
        type:String,
        default:'',
      },
      parmDialog: {
        type: Object,
        default: ()=>{
          return new Object()
        }
      },
      //被删除的数据
      deleteTags:{
        type: Object,
        default: () => {
          return {
          };
        }
      },
      tags:{
        type: Array,
        default:null
      },
      ProcessType:{
        type:String,
        default:''
      }
    },
    data () {
      return {
        searchTxt:'',
        processlist:[],
        //默认树形
        // tree 默认配置
        defaultProps:{
          label:'name',
          children: 'children',
          disabled:(data,node)=>{
            var _this  = this;
            // all Categroy  process version activity
            if(_this.parmDialog.typeIframe==='All'){
              return false
            }
            if(data.typeName !== 'All') {
              if(_this.parmDialog.typeIframe){
                let iframeArray = [];
                iframeArray = _this.parmDialog.typeIframe.split(',');
                if(iframeArray.includes(data.typeName)){
                  return false;
                }else{
                  return true;
                }
              }else {
                if(data.typeName===_this.ProcessType){
                  return false
                }else {
                  return true
                }
//                return true;
                //data.typeName= _this.ProcessType
              }
            }
          },
          // 设置是否是叶子节点 显示头部小图标
          isLeaf:(data,node)=>{
            if(data.isLast){
              return true;
            }else{
              return false;
            }
          }
        },
        // 树加载loadding
        loading:false,
        // 当前点击 tree 的 node
        //第一次加载树标识
        isFristLoadTree:true,
        //点击当前节点是否触发子节点
        checkStrictly:true,
        defaultCheckedKeys:[],
        currentNode:null,
        current:{},
        userPickUrl:baseUrl //用户控件Url
      };
    },
    created: function (node) {
      var _this = this;
      // `debounce` 是一个通过 Lodash 限制操作频率的函数。
      _this.debouncedSearchTree = debounce(_this.getTreeList,null);
    },
    mounted() {
      if(this.isMountedGetData){
        this.debouncedSearchTree(null);
      }
    },
    watch: {
      //单独删除数据
      tags: {
        //深度监听，可监听到对象、数组的变化
        handler: function (newVal, oldVal) {
          if (newVal) {
            this.currentNode = newVal;
            this.getDefaultCheckedKeys();
          }
        },
        deep: true,
      },
      ProcessType:{
        handler: function (newVal, oldVal) {
          if (newVal) {
            if(this.isMountedGetData){
              this.debouncedSearchTree(null);
            }
          }
        },
        deep: true,
      }
    },
    methods: {
      clearText(){
        this.searchTxt = '';
      },
      //获取 已选择数据的id
      getDefaultCheckedKeys() {
        this.$nextTick(() => {
          //多选
          if (this.mutilselectCheck === 'true') {
            let nodes = [];
            if (this.currentNode && this.currentNode.length > 0) {
              this.currentNode.forEach(v => {
                nodes.push(v.id);
              });
            }
            this.$refs.systemRoleTree.setCheckedKeys(nodes);
          } else {
            //单选
            if (this.currentNode.length > 0) {
              this.$refs.systemRoleTree.setCurrentKey(this.currentNode[0].id);
            } else {
              this.$refs.systemRoleTree.setCheckedKeys([]);
            }
          }
        });
      },

      loadNodeData(node, resolve) {
        if (node.level === 0) {
          return;
        }
        if(node.data.children && node.data.children.length > 0){
          resolve(node.data.children);
          this.$emit('load-node-data');
        }else{
          if(node.data.typeName==='Process' || node.data.typeName==='Version'|| node.data.typeName==='Category'){
            debugger
            this.searchTxt="";
            this.debouncedSearchTree(node,resolve);
            return;
          }
          resolve([]);
        }
        //  设置 选中
        this.$nextTick(()=>{
          this.setCheckedKeys(this.defaultCheckedKeys);
        })
      },
      /**
       * 获取组织数据
       * @param {String} code 当前选择组织架构的code
       * @param {String} aId  当前查询公司Id
       * @param {Function} resolve  懒加载回调函数
       * @param {String} currentKey  设置默认选中的节点Id
       * @param {Boolean} first  是否初次加载数据
       */
      getTreeList(node,resolve,filter){
        let _this = this;
        let param = {};
        let type = '';
        if(!_this.parmDialog.typeIframe){
          type = _this.ProcessType;
        }else {
          type = _this.parmDialog.typeIframe;
        }
        let searchNodeId = ''; //baf7ed9d-f93b-4c24-a33e-0e65fb5e7b76
        let searchtypeName = type;
        if(node){
          searchNodeId = node.data.id;
          searchtypeName = node.data.typeName;
        }
        //初始状态下
        if(this.searchTxt){
          param = {
            searchParam:this.searchTxt,
            dType: type,
          }
        }else{
          if(node == undefined){
            param = {
              dType: type,
            }
          }
          else {
            if(!node.data.isLast) {
              param = {
                dType: type,
                searchParam:'',
                NodeId: node.data.id,
                recordData: {
                  typeName: node.data.typeName
                }
              }
            }
          }
        }
        let params = {
          url:_this.userPickUrl.Base_Process_URL + '/GetAllProcessTree',
          method: 'get',
          params:param
        };
        _this.httpService(params,(result)=>{
          if(result.data && result.status === 200) {
            if(result.data.data && result.data.data.length > 0){
              let data = this.toTree(result.data.data);
              if(resolve){
                resolve(data);
              }
              else{
                _this.processlist = data;
              }
            }else{
              // 查询无数据
              if(resolve){
                resolve([]);
              }
            }
            //设置 选中
            this.$nextTick(()=>{
              this.setCheckedKeys(this.defaultCheckedKeys);
            })
          }
          _this.$emit('closeLoadding');
        });
      },
      /**
       * 点击 tree 节点
       * @param {Object} data tree 的节点 data
       * @param {Object} node  tree 的节点 Node
       * @param {Element} e  节点组件本身
       */
      nodeClick(data,node,e){
        //清除右键菜单
        this.current = node;
        this.$emit('node-click',data,node,e);
      },
      /**
       * 当前选中节点变化时触发的事件
       * @param {Object} data tree 的节点 data
       * @param {Object} node  tree 的节点 Node
       */
      currentChange(data,node){

      },
      /**
       * 节点选中状态发生变化时的回调
       * @param {Object} data  tree 的节点 data
       * @param {Boolean} self 节点本身是否被选中
       * @param {Element} self  tree 的节点本身
       */
      checkChange(data,self,child){
        this.$emit('check-change',data,self,child);
      },
      /**
       * 当复选框被点击的时候触发
       * @param {Object} data tree 的节点 data
       * @param {Object} node  tree 的节点 Node
       */
      check(data, node) {
        this.defaultCheckedKeys = node.checkedKeys;
        //用于portal多选设置

        //单选

        if (this.mutilselectCheck === 'false') {
          let dataId = data.id;
          this.defaultCheckedKeys = this.defaultCheckedKeys.filter((item) => {
            return item === dataId;
          });
          if (node.checkedKeys && node.checkedKeys.length > 0) {
            this.currentNode = [data];
            let temp = {};
            temp=this.$refs.systemRoleTree.getNode(data.parent_Id);
            for (let item of this.currentNode) {
              item.ProcessType = 'ProcessPick';
              item.type  = item.typeName;
            }
          } else {
            this.currentNode = [];
          }
          this.$refs.systemRoleTree.setCheckedKeys([]);
          this.$refs.systemRoleTree.setCheckedKeys(this.defaultCheckedKeys);
        }
        //多选
        else {
          this.defaultCheckedKeys = node.checkedKeys;
          this.currentNode = node.checkedNodes;
          for (let item of this.currentNode) {
            item.ProcessType = 'ProcessPick';
            item.type = item.typeName;
            item.ProcessType = 'ProcessPick';
            item.CategoryId = data.categoryId;
            item.CategoryName = data.categoryName;
            item.ProcessCode = data.processCode;
            item.ProcessName = data.processName;
            item.VersionId = data.versionId;
            item.VersionName = data.versionName;
            item.ActivityId = data.activityId;
            item.ActivityName = data.activityName;
          }
        }
        this.$emit('ProcessCheck', this.currentNode);
      },
      /**
       * 通过 key / data 设置某个节点的勾选状态
       * @param {String|Object} obj 当前要删除的数据
       * @param {Boolean} checked 当前要删除的数据
       * @param {Boolean} deep 当前要删除的数据
       */
      setChecked(obj,checked=false, deep=false){
        let _this = this;
        //多选
        if( _this.$refs.systemRoleTree){
          _this.$nextTick(()=>{
            _this.$refs.systemRoleTree.setChecked(obj,checked, deep);
          });
        }
      },
      /**
       * 通过 key 设置某个节点的当前选中状态
       * @param {String|Object} key 当前要删除的数据
       */
      setCurrentKey(key){
        let _this = this;
        _this.$nextTick(()=>{
          if(key && _this.$refs.systemRoleTree) {
            _this.$refs.systemRoleTree.setCurrentKey(key);
            let node = _this.$refs.systemRoleTree.getNode(key);
            if (node) {
              //获取 event
              _this.$emit('node-click', node.data, node, _this.$refs.systemRoleTree.$el);
            }
          }
        });
      },
      /**
       * 通过 keys 设置目前勾选的节点
       * @param {Array} keys 勾选节点的 key 的数组
       * @param {Boolean} leafOnly 若为 true 则仅设置叶子节点的选中状态
       */
      setCheckedKeys(keys, leafOnly=false){
        let _this = this;
        _this.$nextTick(()=>{
          this.$refs.systemRoleTree.setCheckedKeys(keys,leafOnly);
        });
      },
      getNode(id){
        return this.$refs.systemRoleTree.getNode(id);
      },
      getCheckedNodes(){
        return  this.$refs.systemRoleTree.getCheckedNodes();
      },
      /**
       * list to tree
       * @param {Array} data 数据
       * @returns Array
       */
      toTree(data) {

        if (data == null || data.length == 0) return [];
        let list = [];
        let val = [];
        let map = {};
        for (let item of data) {
          let obj = Object.assign({}, item);
          obj.pId = obj.parent_Id;
          list.push(obj);
        }

        list.forEach(function(item) {
          delete item.children;
        });
        list.forEach(function(item) {
          map[item.id] = item;
        });

        list.forEach(function(item) {
          var parent = map[item.pId];
          if (parent) {
            (parent.children || (parent.children = [])).push(item);
          } else {
            val.push(item);
          }
        });
        return val;
      },
      //httpService 请求接口
      httpService(params,coolback){
        let _this = this;
        _this.loading = true;
        _this.$emit('openLoadding');
        _this.$axios(params).then(result => {
          _this.loading = false;
          coolback(result);
        }).catch(error => {
          _this.loading = false;
          _this.$emit('closeLoadding');
          throw error;
        });
      },

      //获取二级节点
      getSecode(data){
        var _this = this;
        var data={
          ParentID: data.data.id,
          parentId: -1,
          context:  data.data.title,
          isLeaf: data.data.isLast,
          level: data.level,
          spread: false,
        };
        let params = {
          url: _this.userPickUrl.Base_M3_URL + '/UserPick/GetChildOrgTree',
          method: 'post',
          data:data
        };
        if(!data.isLast){
          _this.$axios(params).then(result=>{

          })
        }
      },
      //搜索流程
      handleSearch(){
        this.getTreeList();
      },
      //单个删除
      onlyDelete(val,oldValue) {
        if(this.mutilselectCheck==='false'){
          this.$refs.systemRoleTree.setCheckedKeys([]);
        }
        else {
          let Arr = [];
          Arr.push(oldValue.id);
          this.$refs.systemRoleTree.setCheckedKeys(Arr);
        }
      },
    },
    computed: {
      personTableHeight() {
        return this.processlist.length > 0 ? 185 : 223;
      },
    }
  };
</script>

<style>
  .dialogScroll{
    height: 340px;
    overflow-x: auto;
  }
</style>




