<template>
  <div class="choosePerson">
      <!-- 人员开始-->
        <div class="choose-person-container" >
          <div class="content-body">
            <el-row>
              <!-- 左侧树 begin -->
              <el-col :span="10" class="treeWidth">
                  <div class="tree-container">
                      <organization-tree
                        ref="tree"
                        v-model="code"
                        @node-click="handleClick">
                      </organization-tree>
                  </div>
              </el-col>
              <!-- 左侧树 end -->
              <!-- 右侧表格 begin -->
              <el-col :span="14">
                <choosentable :tags="tags"
                              :type="type"
                              :mutilselectCheck="mutilselectCheck"
                              :parmDialog="parmDialog"
                              :currentOrgId="currentOrgId"
                              :deleteTags="deleteTags"
                              ref="employeeTable"
                              @choosePersonTag="choosePersonTag"
                              class="choosePersonTable">
                </choosentable>
              </el-col>
              <!-- 右侧表格 end -->
            </el-row>
          </div>
        </div>
      <!-- 人员结束-->

    <!-- tab切换标签 end -->

  </div>

</template>
<script>

import OrganizationTree from '@/components/biz/tree.vue';
import choosentable from '@/components/biz/choosenTable'
export default {
  name: 'ChoosepersonNew',
  data () {
    return {
      searchid:'',
      loading: true,
      // 常用联系人集合
      deletechooseheight:'deletechooseheight',
      deletechoosecontent:'deletechoose-content',
      activeName:'first',
      //组织Id
      currentOrgId: '',
      code:'',
    };
  },
  components: {
    OrganizationTree,
    choosentable
  },
  props: {
    tags:{
      type: Array,
      default:null
    },
    deleteTags:{
      type: Object,
      default: () => {
        return {
        };
      }
    },
    type:{
      type:String,
      default:''
    },
    mutilselectCheck:{
      type:String,
      default:''
    },
    parmDialog: {
      type: Object,
      default: ()=>{
        return new Object()
      }
    },
  },
  computed: {
    personTableHeight() {
      return this.tableData.length > 0 ? 185 : 223;
    },
    userTableHeight() {
      return this.userdata.length > 0 ? 185 : 223;
    }
  },
  created () {

  },
  watch:{

  },
  mounted() {
    this.$nextTick(()=>{
      this.$refs.employeeTable.getPerson()
    });
  },
  methods: {
    // 点击组织树节点获取员工
    handleClick (data,node) {
      let _this = this;
      _this.currentOrgId = data.id;
      _this.$refs.employeeTable.getPerson(null,data.id,null);
    },
    //获取 人员Id
    getPersonEmpId(list){
      let empIds = [];
      for(let item of list){
        let sysid=item.empId;
        if(sysid==undefined && !item.agent){
          empIds.push(item.id);
        }
        if(sysid==undefined && item.agent){
          empIds.push(item.agent);
        }
        else{
          empIds.push(item.empId);
        }

      }
      return empIds;
    },
   //操作tags
    choosePersonTag(val){
      this.$emit('empSelectTags',val);
    },
  },
};
</script>

<style lang="scss" scoped>
.serchCOL{
  display: flex;
  justify-content: space-between;
  .searchText{
  }
  .searchNext{
    display: flex;
    justify-content: space-between;
  }
}
</style>



