<!-- 选择流程分类 -->
<template>
  <div class="originater_dialog newProcess_dialog">
    <div :class="[componentParam.closeShow?'pc-candidates':'pc-candidates pc-background']">
      <div class="dialog_content">
        <el-main style="position:relative;" >
          <div class="top" v-show="!componentParam.closeShow">
            <span>流程分类选择</span>
            <span style="font-size: 30px;cursor: pointer" @click="close">×</span>
          </div>
          <el-tabs type="card"
               v-loading="imgLoading"
              element-loading-text="加载中..."
              element-loading-background="rgba(255, 255, 255, 0.8)">
            <el-tab-pane class="candidates-recently">
              <span slot="label">
                <i class="iconfont icon-todos"></i>&nbsp;流程</span>
              <div class="title  title-border">
                <!--** 面包屑 begin -->
                <el-breadcrumb separator-class="el-icon-arrow-right" :title="tooltipclassificationName">
                  <el-breadcrumb-item>流程分类选择</el-breadcrumb-item>
                  <el-breadcrumb-item>{{classificationName }}</el-breadcrumb-item>
                </el-breadcrumb>
                <!--** 面包屑 end -->
                <span class="rightInput">
                  <el-input placeholder="请输入流程分类名" prefix-icon="el-icon-search" v-model="treeSearch"  @keyup.enter.native="enterSearch" size="mini">
                  </el-input>
                </span>
              </div>
              <div class="content clearfix">

                <div class="content-left">
                  <div class="title">
                    <!-- 组织 集团 -->
                    <el-form style="width: 100%;" :inline="true" class="demo-form-inline init-organize-box">
                      <el-form-item style="width: 100%;" label="">
                          <el-select style="width: 100%;" ref='selectTree' v-model="organizeName" placeholder="组织" popper-class="tree-list-box">
                            <el-option  style="height:auto;padding:0px 0px;" value="" :disabled = true>
                              <div>
                                <el-tree
                                :data="organizeList"
                                :highlight-current="true"
                                node-key='orgID'
                                default-expand-all
                                :expand-on-click-node = false
                                empty-text='暂无数据'
                                :props="props"
                                @node-click="selectTreeClick"
                                ref="orgtree"></el-tree>
                              </div>
                            </el-option>
                          </el-select>
                      </el-form-item>
                    </el-form>
                  </div>

                  <el-tree :data="treeList" node-key="id"
                    :highlight-current="true"
                    :props="defaultProps"
                    empty-text=''
                    :expand-on-click-node = false
                    @node-click="handleNodeClick"
                    :filter-node-method="filterNode"
                    ref="classificationTree" >
                  </el-tree>
                </div>
                <div class="content-right">
                  <!-- 单选时 结构 -->
                  <el-checkbox v-if="!componentParam.oneCheck" :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange" style="padding-top:6px;padding-left:20px;">全选
                  </el-checkbox>
                  <el-checkbox-group class="theRadio" v-if="componentParam.oneCheck" v-model="checkedCities">
                    <el-checkbox-button v-for="item in cities" :label="item" :key="item.id" :checked="item.checked">
                      <div class="media">
                        <div class="media-body" @click="clickDepartment(item)" @dblclick="dblclickCheckedCitie(item)">
                          <h4 class="media-heading iconfont icon-process1" :title="item.name">{{(item.name.length>30)?item.name.substr(0,30)+"...":item.name}}</h4>
                        </div>
                      </div>
                    </el-checkbox-button>
                  </el-checkbox-group>
                  <!-- 多选时 结构 -->
                  <el-checkbox-group class="mut-check" v-else v-model="checkedCities" @change="handleCheckedCitiesChange">
                    <el-checkbox v-for="item in cities" :label="item" :key="item.id" :checked="item.checked">
                      <div class="media">
                        <div class="media-body">
                          <h4 class="media-heading" :title="item.name">{{(item.name.length>30)?item.name.substr(0,30)+"...":item.name}}</h4>
                        </div>
                      </div>
                    </el-checkbox>
                  </el-checkbox-group>
                </div>
                <!-- 设置多选时 显示选择的数据 -->
              </div>
              <div class="addrecently-box" v-bind:class="{ pullup: isActive }">
                <div class="addrecently">
                  <div class="media">
                    <div class="pull" @click="pullBtnHeightChange"></div>
                    <ul class="clearfix">
                      <!-- 清空按钮 -->
                      <div class="clearBtn" v-show="selectedCities.length>0" @click="clearDept"></div>
                      <li v-for="(project,index) in selectedCities" :key="index">
                        <div class="media-body">
                          <i class="el-icon-circle-close" @click="deleteDept(index)"></i>
                          <span class="media-heading">{{project.name}}</span>
                        </div>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>

              <div class="addrecently-footer">
                <el-button type="primary" @click="sureSubmit" icon="el-icon-circle-check-outline" size="mini">确定
                </el-button>
                <el-button type="primary" @click="close" icon="el-icon-circle-close-outline" size="mini">取消</el-button>
              </div>
            </el-tab-pane>
          </el-tabs>
        </el-main>
      </div>
    </div>
  </div>
</template>

<script>
// `debounce` 是一个通过 Lodash 限制操作频率的函数。
import {debounce} from "lodash";
import util from "../../js/util";

export default {
  name: "processClassification",
  props: {
    //主窗口传的参数
    componentParam: {
      //弹窗类型
      componentType: {
        type: String,
        required: true
      },
      //需要 回填到组件上的 数据
      backfillData: {
        type: Array,
        default:new Array(),
        required: true
      },
      //子组件是否单选(true单选，false不是单选)
      oneCheck: {
        type: Boolean,
        default: false,
        required: true
      },
      //控制弹窗头部标题显示隐藏(默认显示)
      closeShow: {
        type: Boolean,
        default: true,
        required: true
      },
      type: Object,
      required: true
    }
  },
  data() {
    return {
      tooltipclassificationName: "", //组织全名显示tip
      classificationName: "", //面包屑的内容   组织名
      treeSearch: "", //组织搜索
      treeList: [], //树数据
      defaultKeys: [], //tree默认展开节点的key
      //tree 默认设置
      defaultProps: {
        id: "id",
        children: "Child",
        label: "name"
      },
      checkedCities: [], //列表绑定数据
      selectedCities: [], //选中数据
      selectOrgs: [], //保存选择的组织全结构
      isActive: false, //控制折叠状态
      checkAll: false, //全选、反选状态
      isIndeterminate: false, //checkbox恢复初始
      cities: [], //右侧流程列表数据
      currentId: "", //存当前选择的组织orgId
      rootNode: {},
      oldtime:0, //双击时间
      //组织 下拉tree
      props: {
        label:'orgName',
        value: 'orgID',
        children: 'm_ChildOrg'
      },
      organizeList: [],//组织数据
      activeOrganizeName:[],//当前选中的组织名称
      organizeName:'',//下拉框显示值
      //当前选中的组织
      organize:{orgID:''},
      imgLoading:false
    };
  },
  watch: {
    // 流程分类 搜索框 侦听
    treeSearch(val) {
      var that = this;
      that.$refs.classificationTree.filter(val.trim());
    }
  },
  created: function (param) {
    let that = this;
    that.imgLoading = true
    Promise.all([that.getOrganizeList(), that.getUserOrganize()]).then(result => {
      if(result[0]){
        //设置默认当前用户所属组织
        that.organize.orgID = result[1] ||  that.organizeList[0].orgID
        let node = that.$refs.orgtree.getNode(that.organize.orgID);
        if(node){
          that.$refs.orgtree.setCurrentKey(that.organize.orgID);
          that.setOrganize(node);
        }
        //获取当前用户默认组织的 流程目录
        that.getCategoryOfOrganize(that.organize.orgID).then(()=>{
          that.imgLoading = false
        })
      }
    }).catch(error => {
      that.imgLoading = false
      that.$message.error(error)
    })
  },
  mounted() {
    if (this.componentParam.backfillData.length > 0) {
      for (var item of this.componentParam.backfillData) {
        this.selectedCities.push(item);
      }
    }
  },
  methods: {
    /**
      * 点击下拉 orgtree
      * @param {Object} data 当前节点数据
      * @param {Object} node  当前点击节点
      */
    selectTreeClick(data,node){
      let that = this;
      that.$refs.selectTree.visible = false
      that.organize = data;
      that.setOrganize(node)
      that.imgLoading = true
      //获取 流程分类数据
      that.getCategoryOfOrganize(data.orgID).then(()=>{
        that.imgLoading = false
      })
      //初始右侧数据
      that.cities = []
      that.checkAll = false          //全选、反选状态
      that.isIndeterminate = false   //checkbox恢复初始
    },
    /**
      * 设置 下拉 选中 数据
      * @param {Object} node 当前点击节点
      */
    setOrganize(node){
      this.activeOrganizeName= [];
      this.getOrgPrarentNode(node); // 保存路径
      this.organizeName = this.activeOrganizeName.reverse().join('/');
    },
    /**
      * 获取用户所在部门
      * @returns {Promise}
      */
    getUserOrganize() {
      let that = this
      return new Promise((resolve, reject) => {
        that.$axios.post("/api/getorg", {}).then(res => {
            if(res.data.getOrgList.length >0){
              let data = res.data.getOrgList[0].orgid
              resolve(data)
            }else{
              resolve(null)
            }
          }).catch(err => {
            reject(`获取用户所在部门失败!ERROR MESSAGE:${err}`)
          })
      })
    },
    /**
      * 获取组织数据
      * @returns {Promise}
      */
    getOrganizeList() {
      let that = this
      return new Promise((resolve, reject) => {
        that.$axios.post("/api/companytree/page", {}).then(res => {
          if (res.data.companyTreeList.length > 0) {
            that.organizeList = res.data.companyTreeList
            resolve(true);
          }else{
            resolve(false);
          }
        }).catch(err => {
          reject(`获取组织数据失败!ERROR MESSAGE:${err}`)
        })
      })
    },
    /**
      * 通过组织获取组织下流程分类
      * @param {String} orgID 当前选中组织Id
      * @returns {Promise}
      */
    getCategoryOfOrganize(orgID) {
      let that = this
      let obj = {
        orgId: orgID, // 组织id
        isUse: 1, // 首页
        name: that.treeSearch, // 搜索的值
        isHide: 1, // 是否是从k2发起
        isDailog: "1"
      };
      return new Promise((resolve, reject) => {
        that.$axios.post("/api/processCategory", obj).then(res => {
          if (res.data.result.length == 0) {
            that.$message.success("暂无数据")
            that.treeList = []
          } else {
            let data = util.toTree(res.data.result,'id','pid','Child');
            // 删除pid!=0的根节点
            for (let i = 0; i < data.length; i++) {
              if (data[i].pid != 0) {
                data.splice(i, 1);
                i--;
              }
            }
            that.treeList = data
          }
          resolve(true)
        }).catch(err => {
          reject(`查询列表数据失败!ERROR MESSAGE:${err}`)
        })
      })
    },
    /**
      * 获取 组织 所有上级节点
      * @param {Object} node 当前点击节点
      */
    getOrgPrarentNode(node) {
      if (node.data.orgName != undefined) {
        this.activeOrganizeName.push(node.data.orgName);
      }
      if (node.parent != undefined) {
        this.getOrgPrarentNode(node.parent);
      }
    },
    /**
      * 搜索 流程tree 数据
      * @param {String} value 当前输入的搜索条件
      * @param {Object} data tree节点 数据
      * @returns {Boolean} 要显示的节点
      */
    filterNode(value, data) {
      if (!value) return true;
      return data.name.indexOf(value) !== -1;
    },
    /**
     * 点击tree 右边显示当前节点的子节点数据
     * @param {Object} data tree节点 数据
     * @param {Object} node tree节点
     */
    handleNodeClick(data, node) {
      var that = this;
      var myDate = new Date();
      var now = myDate.getTime();
      if (that.oldtime && that.rootNode) {
        var over = now - that.oldtime;
        //0.5秒内连续点击同一个节点 模拟双击事件
        if (over < 500 && that.rootNode.id == node.id) {
          that.clickDepartment(data);
          that.getChildNode(node.data.Child);
          setTimeout(function(){
             that.$emit("sureSubmit", {  type: that.componentParam.componentType, list: that.selectedCities });
          },300);
          return;
        }
      }
      that.oldtime = now;  //保留当前点击的节点 时间
      that.rootNode = node;//保留当前点击的节点

      that.displayContents(node.data);
      that.selectOrgs = [];
      that.getPrarentNode(node);
      that.disposeOrganName(data.name);
    },
    /**
     * 显示 流程分类
     * list  数据
     */
    displayContents(list) {
      var that = this;
      //已经勾选的数据 id
      var ids = that.getId(that.selectedCities);
      that.cities = []
      that.checkedCities = []
      //初始 勾选框状态
      that.checkAll = false
      that.isIndeterminate = false
      //有子节点  显示子节点
      if (list.hasOwnProperty("Child")) {
        for (var item of list.Child) {
          that.cities.push(item);
        }
        //过滤已经选择数据
        for (var item of list.Child) {
          if (ids.indexOf(item.id) > -1) {
            that.checkedCities.push(item);
          }
        }
      } else {
        //没有子节点显示 本身
        //过滤已经选择数据
        if (ids.indexOf(list.id) > -1) {
          that.checkedCities.push(list);
        }
        that.cities.push(list);
      }
      var citiesIds = this.getId(this.cities);
      //设置 记录 当前勾选数量
      var checkedCount = 0
      for(let item of that.checkedCities){
        if (citiesIds.indexOf(item.id) !== -1) {
          checkedCount++
        }
      }
      //设置勾选状态
      that.checkAll = checkedCount > 0 && checkedCount === that.cities.length;
      that.isIndeterminate =
        that.checkedCities.length > 0 &&
        that.checkedCities.length < that.cities.length;
    },

    //单选 双击 右侧列表直接选中赋值
    dblclickCheckedCitie(item) {
      this.clickDepartment(item);
      this.$emit("sureSubmit", {
        type: "new_process_dialog",
        list: this.selectedCities
      });
    },
    /**
     * 单选 点击右侧列表
     */
    clickDepartment(item) {
      var that = this;
      this.selectedCities.splice(0, this.selectedCities.length);
      this.selectedCities.push(item);
      this.checkedCities.splice(0, this.checkedCities.length);
      this.checkedCities.push(item);
      //获取tree
      var node = that.$refs.classificationTree.getNode(item.pid);
      that.selectOrgs = [];
      that.getPrarentNode(node);
      item.tooltipclassificationName = that.disposeOrganName(item.name);
    },
    // 全选/反选
    handleCheckAllChange(val) {
      //获取 所有已选 ids
      var ids = this.getId(this.selectedCities);
       this.checkedCities = [];
      if (val) {
        for (var it of this.cities) {
          if (ids.indexOf(it.id) === -1) {
            this.selectedCities.push(it);
          }
          this.checkedCities.push(it);
        }
        this.checkAll = true;
        this.isIndeterminate = false;
      } else {
        for (var it of this.cities) {
          if (ids.indexOf(it.id) > -1) {
            var index = -1;
            for (var item in this.selectedCities) {
              if (this.selectedCities[item].id == it.id) {
                index = item;
              }
            }
            if (index != -1) {
              this.selectedCities.splice(index, 1);
            }
          }
        }
        this.checkAll = false;
        this.isIndeterminate = false;
      }
    },

    /**
     * 右侧列表勾选, 多选走此方法
     */
    handleCheckedCitiesChange(value) {
      //1、清空已选里面 包含 列数据
      var selectList = [];
      for (var item of this.selectedCities) {
        selectList.push(item);
      }
      this.selectedCities.splice(0, this.selectedCities.length);
      var citiesIds = this.getId(this.cities);
      var data = []
      for (var it in selectList) {
        if (citiesIds.indexOf(selectList[it].id) == -1) {
          data.push(selectList[it]);
          checkedCount++
        }
      }
      this.selectedCities = data;

      //2、把已选加入选中数据
      var item = [];
      for (var it of value) {
        item.tooltipclassificationName = this.disposeOrganName(it.name);
      }
      var ids = this.getId(this.selectedCities);
      for (var it of value) {
        if (ids.indexOf(it.id) === -1) {
          this.selectedCities.push(it);
        }
      }
      //设置 记录 当前勾选数量
      var checkedCount = 0
      for(let item of value){
        if (citiesIds.indexOf(item.id) !== -1) {
          checkedCount++
        }
      }
      this.checkedCities = value;
      this.checkAll = checkedCount === this.cities.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.cities.length;
    },

    /**
     * 控制弹窗底部已选区域展开收起
     * style by pcCandidates.scss
     */

    pullBtnHeightChange() {
      this.isActive = !this.isActive;
    },

    // 确认
    sureSubmit() {
      this.$emit("sureSubmit", {
        type: this.componentParam.componentType,
        list: this.selectedCities
      });
    },
    //获取节点Id
    getId(list) {
      var id = [];
      for (var citie of list) {
        id.push(citie.id);
      }
      return id;
    },
    //折叠 上面选择部分
    pullBtnHeightChange() {
      this.isActive = !this.isActive;
    },
    //关闭弹窗
    close() {
      this.$emit("hideComponent");
    },
    // 删除所选组织
    deleteDept(index) {
      //初始 勾选框状态
      this.checkAll = false
      this.isIndeterminate = false
      for (var i in this.checkedCities) {
        if (this.checkedCities[i].id == this.selectedCities[index].id) {
          this.checkedCities.splice(i, 1);
        }
      }
      this.selectedCities.splice(index, 1);
      this.checkAll = this.checkedCities.length === this.cities.length && this.cities.length > 0;
      this.isIndeterminate =
        this.checkedCities.length > 0 &&
        this.checkedCities.length < this.cities.length;
    },
    // 清空 所选组织
    clearDept() {
      this.checkedCities.splice(0, this.checkedCities.length);
      this.selectedCities.splice(0, this.selectedCities.length);
      this.checkAll = false;
      this.isIndeterminate = false;
    },
    /**
     * 处理流程全路径
     *  return organName
     */
    disposeOrganName(orgNames) {
      var organName = "";
      //获取完整的组织结构名称
      for (var it of this.selectOrgs) {
        organName += `${it.text} > `;
      }
      organName = organName.trim();
      organName = organName.substr(0, organName.length - 1);
      this.tooltipclassificationName = organName;
      if (organName.length > 30) {
        this.classificationName = organName.substr(0, 30) + "...";
      } else {
        this.classificationName = organName;
      }
      return organName;
    },
    //获取所有上级节点
    getPrarentNode(pNode) {
      if (!pNode){ return}
      if (pNode.data && pNode.data.name != undefined) {
        this.selectOrgs.unshift({
          text: `${pNode.data.name}`,
          petId: `${pNode.data.id}`,
          PID: `${pNode.data.pid}`
        });
      }
      if (pNode.parent != undefined) {
        this.getPrarentNode(pNode.parent);
      }
    },
    /**
     * 获取选中节点 所有子节点 Id
     * @param {Array} children 选中节点 子节 数据
     */
    getChildNode(children) {
      for (let item of children) {
        this.selectedCities.push(item);
        if (item.hasOwnProperty("Child") && item.Child.length > 0) {
          this.getChildNode(item.Child);
        }
      }
    },
  }
};
</script>


