<!--右侧表格子组件-->
<template>
  <div>
    <div class="choosePerson">
      <!-- 人员开始-->
      <div  class="choose-person-container" >
        <div class="content-body">
          <el-row>
            <!-- 右侧表格 begin -->
            <div class="choosePerson-data">

              <!-- 满行搜索条 begin -->
              <div class="search-condition btn-linkage">
                <el-row>
                  <el-col :span="24" class="serchCOL">
                    <el-input v-model="searchTxt"
                              placeholder="系统角色名称"
                              clearable
                              @keyup.enter.native="handleSearch"
                              class="searchText">
                    </el-input>
                    <div class="searchline">
                      <el-button type="primary"
                                 icon="el-icon-search"
                                 @click="handleSearch"
                                 class="choosenBtn">搜索
                      </el-button>

                    </div>
                  </el-col>
                </el-row>
              </div>
              <!-- 满行搜索条 end -->

              <el-col :span="24">
                <!--<el-scrollbar class="scroll-bar">-->
                <!-- 表格 begin -->
                <div class="table-box">
                  <el-table
                    ref="multipleSys"
                    :highlight-current-row="true"
                    :data="systemRoleTable"
                    :height="personTableHeight"
                    border
                    empty-text=""
                    tooltip-effect="dark"
                    style="width: 100%;text-align: center"
                    @select-all="selectAll"
                    @select="selectCheckChange">
                    <el-table-column
                      type="selection"
                      width="55"/>
                    <el-table-column
                      label="角色名称"
                      center
                      prop="DisplayName"
                      class="clcsWidth"
                      show-overflow-tooltip>
                    </el-table-column>
                  </el-table>
                </div>
                <!--</el-scrollbar>-->
              </el-col>

            </div>
            <!-- 右侧表格 end -->
          </el-row>
        </div>
      </div>
      <!-- 人员结束-->
      <!-- tab切换标签 end -->
    </div>
    <!-- 分页 begin -->
    <div class="table-foot">
      <div class="page-sizes-box">
        <page :pageIndex="infoPage.pageIndex"
              :total="infoPage.total"
              :pageSize="infoPage.pageSize"
              :pageSizesList="infoPage.pageSizesList"
              @handleSizeChange="handleSizeChange"
              @handleCurrentChange="handleCurrentChange">
        </page>
      </div>
    </div>
    <!-- 分页 end -->
  </div>
</template>
<script>
  import page from '@/plugins/page';
  import { baseUrl } from '../../../static/commonApi';
  //右侧表格子组件
  export default {
    name: 'choosenTable',
    data () {
      return {
        loading: true,
        //分页信息
        infoPage:{
          pageIndex: 1,
          pageSize: 10,
          currentPage:1,
          total:0,
          pageSizesList: [10,20],
        },
        deletechooseheight:'deletechooseheight',
        deletechoosecontent:'deletechoose-content',
        // tab标识
        activeName:'first',
        tabtoggle:0,
        //组织Id
        currentSelectOrganizationId: '',
        //单选
        currentRow:null,
        //人员最终选择数据
        usertags:[],
        // 是否活动面板
        isActive:false,
        //搜索框值
        searchTxt:'',
        // 所有选中的人员
        systemRoleTable: [],
        newisEmptyApplicant:false,
        multipleSys: [],
        dialogTags:[],//存取的数组
        userPickUrl:baseUrl //用户控件Url
      };
    },
    components: {
      page
    },
    props: {
      chooseDatas: {
        type: Object,
        default: () => {
          return {
            row: null,
            name: '',
            id: ''
          };
        }
      },
      deleteTags:{
        type:Object,
        default:()=>{
          return {}
        }
      },
      // 最终选择数据
      tags:{
        type:Array,
        default:()=>{
          return []
        }
      },
      type:{
        type:String,
        default:''
      },
      orgTreeID:{
        type:String,
        default:''
      },
      mutilselectCheck:{
        type:String,
        default:''
      }
    },
    computed: {
      personTableHeight() {
        return this.systemRoleTable.length >  0 ? 185 : 230;
      },
    },
    watch: {
      //监听底部数据
      tags:{
        //深度监听，可监听到对象、数组的变化
        handler: function (newVal,oldVal) {
          if(newVal){
            this.doSelection(this.systemRoleTable,newVal);
          }
          this.dialogTags = this.tags;
        },
        deep:true,
      },
      //单独删除数据
      deleteTags:{
        //深度监听，可监听到对象、数组的变化
        handler: function (newVal,oldVal) {
          this.onlyDelete(this.systemRoleTable,newVal)
        },
        deep:true,
      },
    },
    mounted() {
      this.dialogTags = this.tags;
    },
    methods: {
      // 当前选择变更时
      selectCheckChange (selection, row) {
        row.type = 'SystemRole';
        let _this = this;
        //多选
        if(_this.mutilselectCheck==='true'){
          if(selection.length>=1){
            //第一次选中
            for(let item of selection){
              item.type = 'SystemRole';
              item.UserDisplayName = item.DisplayName;
              item.UserAccount = item.Id;
            }
          }
          else {
            //选中取消勾选
            _this.$refs.multipleSys.toggleRowSelection(row,false);
          }
          let tempIds = _this.getPersonId(_this.systemRoleTable);
          if(_this.dialogTags){
            _this.dialogTags = _this.dialogTags.filter(item => tempIds.indexOf(item.Id) == -1);
            let sysyid = _this.getPersonId(_this.dialogTags);
            for (let item of selection) {
              //防止重复添加相同数据
              if (sysyid.indexOf(item.Id) == -1) {
                _this.dialogTags.push(Object.assign({}, item));
              }
            }
          }
        }
        //单选
        else {
          //第一次选中
          if(selection.length>=1){
            //不直接操作数据
            let arrA  = [..._this.dialogTags];
            //第一次进来操作
            if(_this.dialogTags && _this.dialogTags.length>=1){
              for (let item of arrA){
                //判断如果类型是第二次进来勾选人员，取消上一次勾选的数据
                if(item.type==='SystemRole'){
                  let temp  = selection.filter(item=>item.Id !==row.Id);
                  _this.$refs.multipleSys.toggleRowSelection(temp[0],false);
                  let dataIndex = _this.dialogTags.findIndex(item=>item.type==='SystemRole');
                  _this.dialogTags.splice(dataIndex,1,row)
                }
                //判断如果类型是第一次进来勾选人员，直接勾选，push
                else if(!_this.dialogTags.includes(row.type) && selection.length===1) {
                  _this.dialogTags.push(row);
                  _this.dialogTags=Array.from(new Set(_this.dialogTags))
                }
              }
            }else {
              _this.dialogTags.push(row)
            }
          }
//          //再次选中
          else {
            _this.$refs.multipleSys.toggleRowSelection(row,false);
            let delIndex =  _this.dialogTags.findIndex(item=>item.SysId===row.SysId);
            _this.dialogTags.splice(delIndex,1);
          }
        }
        for(let item of _this.dialogTags){
          if(item.type==='SystemRole') {
            item.UserDisplayName = item.DisplayName;
            item.UserAccount = item.Id;
          }
        }
        //选人组件发送选中的tags
        _this.$emit('chooseOrgTag',_this.dialogTags);
      },

      //获取 人员Id
      getPersonEmpId(list){
        let empIds = [];
        for(let item of list){
          let sysid=item.SysId;
          if(sysid==undefined){
            empIds.push(item.SysId);
          }
          else{
            empIds.push(item.SysId);
          }

        }
        return empIds;
      },
      //点击 每页显示条目数
      handleSizeChange(val) {
        var that = this;
        var pageSize =  val;
        that.infoPage.pageIndex = 1;
        that.infoPage.pageSize = parseInt(pageSize);
        if(_this.searchTxt){
          that.handleSearch()
        }
        else {
          that.getOrg(that.orgTreeID);
        }
        setTimeout(function () {
          that.doSelection(that.systemRoleTable,that.dialogTags);
        },500)
      },
      //点击页码
      handleCurrentChange(val) {
        var _this =this;
        _this.infoPage.pageIndex = val;
        if(_this.searchTxt){
          _this.handleSearch()
        }
        else {
          _this.getOrg(_this.orgTreeID);
        }
        setTimeout(function () {
          _this.doSelection(_this.systemRoleTable,_this.dialogTags);
        },500)
      },
      //设置回显
      setToggleSelect(rows){
        var that = this;
        rows.forEach(rows => {
          that.$nextTick(()=>{
            that.$refs.multipleSys.toggleRowSelection(rows,true);
          });
        });
      },
      // 隐藏全选功能
      selectAll(val){
        var _this = this;
        if(val.length===0){
          //清空素组
          _this.dialogTags.splice(0,_this.dialogTags.length)
        }
        //单选
        if(_this.mutilselectCheck==='false'){
          _this.$nextTick(() => {
            _this.$refs.multipleSys.clearSelection();
          });
          for(let v of val){
            _this.dialogTags = _this.dialogTags.filter((item)=>{
              return item.SysId !==v.id;
            });
          }
        }
        //多选
        else {
          let siSid = _this.getPersonId(_this.dialogTags);
          for (let item of val){
            item.type = 'SystemRole';
            item.UserDisplayName = item.DisplayName;
            item.UserAccount = item.Id;
            if(siSid.includes(item.Id)){

            }else {
              _this.dialogTags.push(item)
            }
          }
        }
        _this.$emit('chooseOrgTag',_this.dialogTags)
      },
      //设置勾选上
      doSelection(val,list) {
        var _this = this;
        if (list && list.length>=1) {
          list.forEach((item) => {
            const index = val.findIndex((v) => v.Id === item.SysId);
            if(index>-1){
              _this.$nextTick(()=>{
                _this.$refs.multipleSys.toggleRowSelection(val[index], true);
              });
            }else {
              _this.$nextTick(()=>{
                _this.$refs.multipleSys.toggleRowSelection(val[index], false);
              });
            }
          });
        }
        else if(list.length === 0){
          _this.$nextTick(()=> {
            this.$refs.multipleSys.clearSelection();
          })
        }
      },
      //获取所有id
      getPersonId(list){
        var onlySid = [];
        for(let item of list){
          let ssid = item.Id;
          if(ssid){
            onlySid.push(ssid)
          }
        }
        return onlySid
      },
      //单个删除
      onlyDelete(val,oldValue) {
        var _this = this;
        const delindex = _this.getPersonId(val);
        const index = delindex.indexOf(oldValue.SysId);
        _this.$refs.multipleSys.toggleRowSelection(val[index], false);
      },
      //搜索
      handleSearch(){
        //处理搜索数据
        let that = this;
        if(that.searchTxt===''){
          that.$message.error('请输入系统角色信息');
          return
        }
        that.getOrgSearch(that.searchTxt)
      },
      // 获取员工数据
      getSystemRole(sysId) {
        let _this = this;
        // 分页与排序信息
        const data = {
          page: _this.infoPage.pageIndex,
          limit: _this.infoPage.pageSize,
          CategoryId: sysId,
        };
        _this.doRest(data, function (result) {
          _this.systemRoleTable = result.data;
          _this.infoPage.total = result.data.length;
          _this.doSelection(_this.systemRoleTable,_this.dialogTags);
        },(err)=>{

        });
      },
      // 调用api请求
      doRest(data, callback) {
        const me = this;
        me.loading = true;
        me.$axios.post(me.userPickUrl.Base_User_URL + '/GetSystemRoleByType',data).then(result => {
          if (result.status === 200) {
            callback(result.data);
            me.loading = false;
          } else {

          }
        })
          .catch(error => {
            me.loading = false;
            throw error;
          });
      },
      //httpService 请求接口
      httpService(params,coolback){
        let _this = this;
        _this.loading = true;
        _this.$axios(params).then(result => {
          _this.loading = false;
          coolback(result);
        }).catch(error => {
          _this.loading = false;
          throw error;
        });
      },
      //获取搜索
      getOrgSearch(val){
        let _this = this;
        // 分页与排序信息
        const data = {
          page: _this.infoPage.pageIndex,
          limit: _this.infoPage.pageSize,
          key:val
        };
        _this.$axios.post(_this.userPickUrl.Base_User_URL + '/GetSystemRoleListByKey',data).then(result => {
          _this.systemRoleTable = result.data.data;
          _this.infoPage.total = result.data.count;
          _this.doSelection(_this.systemRoleTable,_this.dialogTags);
        }).catch(error=>{

        })
      },
    },
  };
</script>

<style lang="scss" scoped>
  .serchCOL{
    display: flex;
    justify-content: space-between;
    .searchText{
    }
    .searchNext{
      display: flex;
      justify-content: space-between;
    }
  }
</style>



